.steps {
  &__title {
    margin-bottom: 40px;
  }

  &__list {
    display: flex;
    justify-content: space-between;
    gap: 24px;
    list-style: decimal;
  }
}

.step {
  position: relative;
  padding: 24px 32px 24px 32px;
  min-height: 253px;
  width: 24%;
  border-radius: 24px;
  background-color: var(--background);

  &::marker {
    content: none;
  }

  &::before {
    content: counter(list-item);
    position: absolute;
    right: 32px;
    top: -18px;
    width: 50%;
    height: 100%;
    color: transparent;
    background-image: linear-gradient(180deg, rgba(186, 26, 26, 0.6) -57.95%, rgba(251, 246, 246, 0.6) 80.45%);
    background-clip: text;
    text-align: right;
    font-size: 200px;
    line-height: 110%;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 16px;
    height: 100%;
    z-index: 2;
  }

  &__title {
    margin-top: auto;
    padding-right: 20%;
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
    color: var(--main);
  }
}

@media (max-width: 1300px) {
  .steps__list {
    flex-wrap: wrap;
  }

  .step {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .steps {
    &__title {
      margin-bottom: 20px;
    }

    &__list {
      display: grid;
      grid-template-columns: 1fr;
      grid-auto-rows: 1fr;
      gap: 20px;
    }
  }

  .step {
    position: relative;
    padding: 18px 20px;
    min-height: 212px;
    height: 100%;
    width: 100%;
    border-radius: 16px;

    &::before {
      right: 22px;
      font-size: 160px;
    }

    &__wrapper {
      gap: 12px;
    }

    &__title {
      font-size: 18px;
    }
  }
}