// about

.about__list {
  display: flex;
  justify-content: space-between;
  gap: 16px;
}

.about__item {
  padding: 16px 20px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  border-radius: 20px;
  background-color: var(--white);

  & span {
    font-size: 32px;
    line-height: 100%;
    font-weight: 600;
  }
}

.about__slider {
  position: absolute;
  bottom: 143px;
  right: 24%;
  width: 642px;
  z-index: 3;
  overflow: hidden;
}

.about__img {
  width: 642px;
  height: 403px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
}

.about__slider-navigation {
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 4;
}

@media (max-width: 1880px) {
  .about__slider {
    right: 12%;
  }
}

@media (max-width: 1550px) {
  .about__slider {
    bottom: 125px;
    right: 34%;
    width: 479px;
  }

  .about__img {
    width: 479px;
    height: 306px;
  }
}

@media (max-width: 1023px) {
  .about__list {
    flex-direction: column;
    gap: 8px;
  }

  .about__item {
    border-radius: 16px;
    gap: 16px;

    & span {
      font-size: 24px;
    }
  }

  .about__slider {
    position: absolute;
    bottom: 66px;
    top: auto;
    left: 50%;
    transform: translateX(-50%);
    right: auto;
    width: 233px;
  }

  .about__img {
    width: 227px;
    height: 145px;
  }
}