// prices

.prices {
  &__title {
    margin-bottom: 40px;
  }

  &__slider {
    overflow: hidden;
  }
}

.price {
  padding: 12px 12px 40px 12px;
  display: flex;
  flex-direction: column;
  gap: 28px;
  height: auto;
  min-height: 557px;
  background-color: var(--background);
  border-radius: 24px;

  &__wrapper {
    padding: 24px 28px;
    border-radius: 16px;
    background-color: var(--white);
  }

  &__name {
    margin-bottom: 43px;
    font-size: 24px;
    line-height: 110%;
  }

  &__number {
    font-weight: 600;
    font-size: 40px;
    line-height: 110%;
    color: var(--main);

    & span {
      font-size: 24px;
      line-height: 110%;
      font-weight: 500;
      color: #ba1a1a99;
    }
  }

  &__list {
    margin: 0 28px;
  }

  &__button {
    margin: auto 28px 0 28px;
  }
}

.price--accent {
  & .price__wrapper {
    color: var(--white);
    background-color: var(--main);
    background-image: url("/assets/img/sections/background/back-price.png");
    background-size: auto 100%;
    background-position: right top;
    background-repeat: no-repeat;
  }

  & .price__number {
    color: var(--white);
  }
}

@media (max-width: 1023px) {
  .prices {
    &__title {
      margin-bottom: 26px;
    }
  }

  .price {
    padding: 6px 6px 20px 6px;
    gap: 20px;
    min-height: 384px;
    border-radius: 16px;

    &__wrapper {
      padding: 10px 14px;
      border-radius: 11px;
    }

    &__name {
      margin-bottom: 26px;
      font-size: 18px;
      line-height: 130%;
    }

    &__number {
      font-size: 22px;

      & span {
        font-size: 18px;
      }
    }

    &__list {
      margin: 0 14px;
    }

    &__button {
      margin: auto 14px 0 14px;
    }
  }

  .price--accent  .price__wrapper {
    background-image: url("/assets/img/sections/background/back-price-mob.png");
  }
}