.contacts {
  margin: 60px 0 140px 0;

  &__wrapper {
    display: flex;
    gap: 24px;
  }

  &__flex {
    padding: 32px 40px 40px 40px;
    display: flex;
    flex-direction: column;
    width: 49%;
    border-radius: 24px;
    background-color: var(--background);
  }

  &__description {
    margin-bottom: 32px;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__list {
    margin-bottom: 32px;
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  &__item {
    position: relative;
    padding-left: 79px;
    display: flex;
    flex-direction: column;
    gap: 10px;

    &::before {
      content: "";
      position: absolute;
      width: 59px;
      height: 59px;
      left: 0;
      top: 0;
      background-color: var(--white);
      border-radius: 12px;
    }
  }

  &__phone,
  &__email,
  &__address {
    font-size: 20px;
    font-weight: 600;
    color: var(--text-accent);
  }

  & a:hover {
    color: var(--black);
  }

  & address {
    font-style: normal;
  }

  &__phone::after,
  &__email::after,
  &__address::after {
    content: "";
    width: 21px;
    height: 21px;
    position: absolute;
    left: 19px;
    top: 17px;
    mask-size: 21px;
    mask-repeat: no-repeat;
    background-color: var(--main);
  }

  &__phone::after {
    mask-image: url("/assets/img/icons/stack.svg#phone");
  }

  &__email::after {
    mask-image: url("/assets/img/icons/stack.svg#mail");
  }

  &__address::after {
    mask-image: url("/assets/img/icons/stack.svg#address");
  }

  &__button {
    margin-top: auto;
    width: 100%;
  }

  &__map {
    height: auto;
    flex-grow: 1;
    border-radius: 24px;
    overflow: hidden;
  }
}

#map {
  width: 100%;
  height: 100%;
}

@media (max-width: 768px) {
  .contacts {
    margin: 40px 0 64px 0;

    &__wrapper {
      flex-direction: column;
      gap: 16px;
    }

    &__flex {
      padding: 16px 20px 20px 20px;
      width: 100%;
    }

    &__description {
      margin-bottom: 24px;
      gap: 12px;
    }

    &__list {
      margin-bottom: 24px;
    }

    &__item {
      padding-left: 65px;

      &::before {
        width: 49px;
        height: 49px;
      }
    }

    &__phone,
    &__email,
    &__address {
      font-size: 18px;
      font-weight: 600;
    }

    &__phone::after,
    &__email::after,
    &__address::after {
      top: 12px;
      left: 14px;
    }

    &__map {
      border-radius: 16px;
      width: 100%;
      height: 587px;
    }
  }
}