/* glob  */

:root {
  --white: #ffffff;
  --black: #220002;
  --main: #bf1f1f;
  --main-dark: #a71d1d;
  --text-accent: #802020;
  --background: #fbf6f6;
  --background-icon: #BF1F1F1A;
  --hover: #9F0D0D;
  --border: #22000221;
}

body {
  position: relative;
  font-family: "Onest", sans-serif;
  color: var(--black);
  font-size: 18px;
  line-height: 130%;
}

.container {
  margin: 0 auto;
  width: 100%;
  max-width: 1720px;
}

.section-offset {
  margin: 140px 0;
}

h1,
h2,
h3,
h4,
h5,
.title,
.title--secondary,
.title--third {
  color: var(--text-accent);
}

.title {
  font-weight: 700;
  font-size: 60px;
  line-height: 110%;
}

.title--secondary {
  font-weight: 600;
  font-size: 48px;
  line-height: 110%;
  text-align: center;
}

.title--third {
  font-weight: 600;
  font-size: 40px;
  line-height: 110%;
}

.no-scroll {
  overflow-y: hidden;
}

.no-scroll::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.541);
  z-index: 2;
}

.block-accent {
  margin: 0 auto;
  max-width: 1880px;
  color: var(--white);
  background-color: var(--main);
  border-radius: 40px;
}

.block-accent .title--secondary,
.block-accent .title--third {
  text-align: left;
  color: var(--white);
}

.button {
  padding: 14px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: max-content;
  min-height: 60px;
  font-weight: 700;
  line-height: 130%;
  background-color: var(--main);
  color: var(--white);
  text-align: center;
  border-radius: 12px;
  transition: background-color 0.7s;
}

.button--inverse {
  color: var(--main);
  background-color: var(--white);
  transition: opacity 0.7s;
}

.button--small {
  padding: 0;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--background-icon);
  border-radius: 12px;
}

.button:hover {
  background: var(--hover);
  transition: background-color 0.7s;
}

.button--small:hover {
  background-color: var(--main);
  transition: background-color 0.7s;
}

.button--small:hover .header__icon {
  fill: var(--white);
}

.button--inverse:hover {
  background-color: var(--white);
  opacity: 0.8;
  transition: opacity 0.7s;
}

.button--more {
  margin: 0 auto;
  margin-top: 32px;
  width: 300px;
}

.button--calc {
  color: var(--black);
  background-color: #2200020d;

  &:hover {
    background-color: #22000236;
  }
}

.button[disabled] {
  background-color: #bc1d1d8c;
  cursor: default;

  &:hover {
    background-color: #bc1d1d8c;
  }
}

.button--calc.button[disabled] {
  background-color: #2200020d;

  &:hover {
    background-color: #2200020d;
  }
}

.input {
  padding: 19px 20px;
  width: 100%;
  height: 60px;
  font-size: 18px;
  background-color: var(--background);
  border-radius: 12px;
}

.input::placeholder {
  font-size: 18px;
  line-height: 130%;
  color: #22000266;
}

input[type="checkbox"] {
  position: relative;
  appearance: none;
  width: 18px;
  min-width: 18px;
  height: 18px;
  left: 0;
  top: 0;
  border: 1px solid var(--border);
  border-radius: 4px;
}

input[type="checkbox"]:checked {
  background-color: var(--main);
}

input[type="checkbox"]:checked::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-image: url("/assets/img/icons/stack.svg#check");
}

address {
  font-style: normal;
}

.list {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.list li {
  padding-left: 17px;
  position: relative;
}

.list li::before {
  content: "";
  width: 6px;
  height: 6px;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  background-color: var(--accent);
  border-radius: 50%;
}

.form {
  position: relative;
  padding: 28px 32px 20px 32px;
  color: #22000299;
  background-color: var(--white);
  border-radius: 24px;

  &__title {
    margin-bottom: 24px;
    padding: 0 50px;
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
    color: var(--text-accent);
    text-align: center;
  }

  &__label {
    position: relative;
    width: 100%;
  }

  &__label--agree {
    display: flex;
    gap: 12px;
  }

  &__agree {
    width: 100%;
    font-size: 14px;
    line-height: 130%;
  }

  &__input {
    width: 100%;

    &::placeholder {
      color: #22000266;
    }
  }

  &__button {
    margin-top: 12px;
    margin-bottom: 20px;
    width: 100%;
  }
}

.swiper-horizontal>.swiper-pagination-bullets,
.swiper-pagination-bullets.swiper-pagination-horizontal,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  display: none;
}

.slider-navigation {
  display: flex;
  gap: 20px;
}

.swiper-button-prev,
.swiper-button-next {
  position: relative;
  margin: 0;
  top: auto;
  left: auto;
  right: auto;
  bottom: auto;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background-color: var(--main);
  cursor: pointer;
}

.block-accent .swiper-button-prev,
.block-accent .swiper-button-next {
  background-color: var(--white);
}

.swiper-button-prev:hover,
.swiper-button-next:hover {
  opacity: 0.8;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 16px;
  background-color: var(--white);
}

.block-accent .swiper-button-next::after,
.block-accent .swiper-button-prev::after {
  background-color: var(--main);
}

.about__slider .swiper-button-next,
.about__slider .swiper-button-prev {
  background-color: var(--main);
}

.about__slider .swiper-button-next::after,
.about__slider .swiper-button-prev::after {
  background-color: var(--white);
}

.swiper-button-next::after {
  mask-image: url("/assets/img/icons/stack.svg#slider-right")
}

.swiper-button-prev::after {
  mask-image: url("/assets/img/icons/stack.svg#slider-left");
}

::-webkit-scrollbar-track {
  background-color: #bf1f1f33;
}

::-webkit-scrollbar-thumb {
  border-radius: 20px;
  background-color: var(--main);
}

::-webkit-scrollbar {
  border-radius: 20px;
  height: 8px;
  width: 8px;
}

summary::marker {
  content: '';
}

summary::-webkit-details-marker {
  display: none;
}

@-webkit-keyframes transformItem {
    0% {
        opacity: 0;
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes transformItem {
    0% {
        opacity: 0;
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }

    100% {
        opacity: 1;
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

.transform-item {
    opacity: 0;
}

.animation-active.transform-item {
    opacity: 1;
    -webkit-animation: transformItem 0.4s forwards;
            animation: transformItem 0.4s forwards;
}

.animation-active.transform-item:nth-child(2n) {
    -webkit-animation: transformItem 0.45s forwards;
            animation: transformItem 0.45s forwards;
}

.animation-active.transform-item:nth-child(3n) {
    -webkit-animation: transformItem 0.55s forwards;
            animation: transformItem 0.55s forwards;
}

.animation-active.transform-item:nth-child(5n) {
    -webkit-animation: transformItem 0.65s forwards;
            animation: transformItem 0.65s forwards;
}

.animation-active.transform-item:nth-child(7n) {
    -webkit-animation: transformItem 0.75s forwards;
            animation: transformItem 0.75s forwards;
}

@-webkit-keyframes animationBtn {
    0% {
        left: -55px;
    }

    100% {
        left: calc(100% + 50px);
    }
}

@keyframes animationBtn {
    0% {
        left: -55px;
    }

    100% {
        left: calc(100% + 50px);
    }
}

@media (max-width: 1750px) {
  .container {
    padding: 0 16px;
  }
}

@media (max-width: 1023px) {
  .main {
    position: relative;
    padding-top: 74px;
  }

  .section-offset {
    margin: 64px 0;
  }

  .title {
    font-size: 32px;
    line-height: 111%;
  }

  .title--secondary,
  .title--third {
    line-height: 110%;
  }

  .title--secondary {
    font-size: 24px;
  }

  .title--third {
    font-size: 22px;
  }

  .swiper-horizontal>.swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    margin-top: 16px;
    display: flex;
    width: 100%;
  }

  .swiper-pagination-bullet {
    width: auto;
    background-color: var(--main);
    opacity: 0.3;
    width: 32%;
    height: 8px;
    border-radius: 20px;
  }

  .swiper-pagination-bullet-active {
    background-color: var(--main);
    opacity: 1;
  }

  .block-accent .swiper-pagination-bullet {
    background-color: var(--white);
  }

  .block-accent .swiper-pagination-bullet-active {
    background-color: var(--white);
  }

  .about__slider .slider-navigation,
  .doctor-inner__slider .slider-navigation {
    display: none;
  }
}

@media (max-width: 768px) {
  body {
    font-size: 16px;
  }

  .main {
    overflow-x: hidden;
  }

  .button {
    padding: 12px 19px;
    min-height: 52px;
  }

  .button--more {
    margin: 0;
    margin-top: 32px;
    width: 100%;
  }

  .block-accent {
    border-radius: 24px;
  }

  .block-accent .title--secondary,
  .block-accent .title--third {
    text-align: center;
  }

  .input {
    height: 52px;
  }

  .form {
    padding: 20px 16px 14px 16px;
    border-radius: 16px;

    &__title {
      margin-bottom: 16px;
      padding: 0;
      font-size: 18px;
    }

    &__label {
      position: relative;
      width: 100%;
    }

    &__button {
      margin-bottom: 16px;
    }
  }

  .slider-navigation {
    display: none;
  }

  ::-webkit-scrollbar {
    display: none;
  }
}