// faq

.faq {
  margin-top: -43px;
  padding-top: 113px;
  overflow: hidden;

  &__title {
    position: relative;
    text-align: left;
    z-index: 2;
  }

  &__subtitle {
    position: relative;
    margin-top: auto;
    z-index: 2;

    & a {
      display: inline-block;
      font-weight: 600;
      border-bottom: 1px solid var(--main);

      &:hover {
        color: var(--main);
      }
    }
  }

  &__wrapper {
    display: flex;
    gap: 12px;
  }

  &__flex {
    position: relative;
    padding: 0 400px 140px 0;
    display: flex;
    flex-direction: column;
    min-height: 653px;
    width: 50%;
  }

  &__img {
    position: absolute;
    right: 0;
    top: -43px;
  }

  &__list {
    padding-bottom: 140px;
    display: flex;
    flex-direction: column;
    gap: 12px;
    width: 49%;
  }
}

.question {
  position: relative;
  padding: 17px 32px 17px 32px;
  display: block;
  width: 100%;
  transition: 0.3s;
  overflow: hidden;
  background-color: var(--background);
  color: var(--text-accent);
  border-radius: 24px;
}

.question[open] {
  padding-bottom: 93px;
}

.question__title {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 20px;
  font-weight: 600;
  cursor: pointer;
  transition: .3s;

  &::marker {
    content: '';
  }

  &::-webkit-details-marker {
    display: none;
  }
}

.question__content {
  margin-top: 17px;
  margin-bottom: -69px;
  padding-right: 10px;
}

.question__button {
  position: relative;
  margin-left: 24px;
  width: 40px;
  min-width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--background-icon);
  transition: background-color 0.7s;
}

.question__button::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 2px;
  height: 16px;
  background-color: var(--main);
  opacity: 1;
  transition: opacity 0.3s;
}

.question__button::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 16px;
  height: 2px;
  background-color: var(--main);
}

.question[open] .question__button::before,
.question[open] .question__button::after {
  background-color: var(--white);
}

.question[open] .question__button {
  background-color: var(--main);
}

.question[open] .question__button::before {
  opacity: 0;
  transition: opacity 0.3s;
}

@media (max-width: 1440px) {
  .faq__wrapper {
    gap: 0;
  }

  .faq__flex {
    min-height: auto;
    width: 60%;
  }

  .faq__list {
    min-height: auto;
    width: 40%;
  }
}

@media (max-width: 1023px) {
  .faq {
    &__title {
      text-align: center;
    }

    &__subtitle {
      margin-top: 16px;
      text-align: center;

      & a {
        border-bottom: none;
      }
    }

    &__wrapper {
      flex-direction: column;
      gap: 20px;
    }

    &__flex {
      position: relative;
      padding: 0;
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &__img {
      display: none;
    }

    &__list {
      width: 100%;
    }
  }

  .question {
    position: relative;
    padding: 17px 20px;
    border-radius: 16px;
  }

  .question[open] {
    padding-bottom: 100px;
  }

  .question__title {
    font-size: 18px;

    &::marker {
      content: '';
    }

    &::-webkit-details-marker {
      display: none;
    }
  }

  .question__content {
    margin-top: 18px;
    margin-bottom: -69px;
    padding-right: 0;
  }

  .question__button {
    width: 32px;
    min-width: 32px;
    height: 32px;
  }

  .question__button::before {
    height: 12px;
  }
}


// faq-page

.faq-page {
  margin: 60px 0 140px 0;

  &__wrapper {
    display: flex;
    gap: 24px;
  }

  & .faq__list {
    padding: 0;
    flex-grow: 1;
  }

  & .text-content__flex-form {
    margin-bottom: auto;
    width: 412px;
  }
}

@media (max-width: 1023px) {
  .faq-page {
    margin: 40px 0 64px 0;

    &__wrapper {
      flex-direction: column;
    }

    & .text-content__flex-form {
      margin-bottom: 0;
      width: 100%;
    }
  }
}