// services

.services {
  position: relative;
  padding: 60px 0 73px 0;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-image:
      url("/assets/img/sections/background/back-services-1.webp");
    background-position: left top;
    background-repeat: no-repeat;
  }

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-image:
      url("/assets/img/sections/background/back-services-2.webp");
    background-position:
      right top;
    background-repeat: no-repeat;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 39px;
    z-index: 2;
  }

  &__top {
    display: flex;
    justify-content: space-between;
  }

  &__link {
    width: 296px;
  }

  &__slider {
    overflow: hidden;
  }
}

.service {
  padding: 40px;
  display: flex;
  flex-direction: column;
  height: auto;
  color: var(--black);
  background-color: var(--white);
  border-radius: 24px;
  box-shadow: 0px 0px 160px 0px #eb5d6233 inset;

  &__icon {
    margin-bottom: 32px;
    width: 60px;
    height: 60px;
  }

  &__name {
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 32px;
    line-height: 110%;
    color: var(--text-accent);
  }

  &__description {
    margin-bottom: 40px;
    opacity: 0.8;
  }

  &__price {
    margin-bottom: 28px;
    font-size: 32px;
    line-height: 100%;
  }

  &__list {
    margin-bottom: 40px;
  }

  &__button {
    margin-top: auto;
  }
}

.list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.list li {
  padding-left: 30px;
}

.list li::before {
  content: "";
  position: absolute;
  width: 18px;
  height: 23px;
  left: 0;
  top: 50%;
  mask-image: url("/assets/img/icons/stack.svg#list");
  background-color: var(--main);
}

@media (max-width: 1023px) {
  .services {
    padding: 64px 0;

    &::before {
      transform: rotate(90deg);
      background-size: 133%;
      left: auto;
      right: 82%;
      top: -31%;
    }

    &::after {
      transform: rotate(90deg);
      background-size: 133%;
      left: auto;
      right: 82%;
      top: auto;
      bottom: -32%;
    }

    &__wrapper {
      gap: 20px;
    }

    &__top {
      display: block;
    }

    &__link {
      display: none;
    }
  }

  .service {
    padding: 20px;
    width: 100%;
    border-radius: 16px;

    &__icon {
      margin-bottom: 16px;
      width: 32px;
      height: 32px;
    }

    &__name {
      margin-bottom: 12px;
      font-size: 20px;
      line-height: 110%;
    }

    &__description {
      margin-bottom: 24px;
    }

    &__price {
      margin-bottom: 19px;
      font-size: 20px;
      line-height: 110%;
    }

    &__list {
      margin-bottom: 32px;
    }
  }

  .list li::before {
    content: "";
    position: absolute;
    width: 16px;
    height: 21px;
    background-size: 16px 21px;
  }
}

@media (max-width: 768px) {
  .services__slider {
    position: relative;
    padding: 0;
    width: 100%;
    overflow: visible;
  }
}