/* header */
.header__top {
  padding: 21px 0 17px 0;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid var(--border);
}

.header__top-text {
  position: relative;
  padding-left: 29px;
  font-size: 17px;
  font-weight: 500;
  color: var(--main);
}

.header__top-text::before {
  content: "";
  position: absolute;
  width: 21px;
  height: 21px;
  left: 0;
  top: 0;
  mask-image: url("/assets/img/icons/stack.svg#clock");
  background-color: var(--main);
  z-index: -1;
}

.header__top-text span {
  font-weight: 400;
  color: var(--black);
}

.header-socials {
  display: flex;
  gap: 20px;
  width: max-content;
}

.header-socials__link {
  display: inline-block;
  font-size: 17px;
  color: var(--main);
  border-bottom: 1px solid var(--main);
}

.header-socials__link:hover {
  color: var(--hover);
}

.header__bottom {
  padding: 17px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  margin-right: 60px;
  display: flex;
  align-items: center;
  gap: 16px;
  width: 215px;
  height: 60px;
}

.logo__text {
  display: flex;
  flex-direction: column;
  gap: 2px;
  font-weight: 700;
  font-size: 20px;
  line-height: 100%;
  color: var(--text-accent);
  white-space: nowrap;
}

.logo__text span {
  font-weight: 400;
  font-size: 18px;
  color: var(--main);
}

.main-navigation {
  margin-right: 32px;
  width: max-content;
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 0 34px;
}

.main-navigation__item {
  position: relative;
  padding: 19px 0;
}

.main-navigation__link {
  position: relative;
  display: flex;
  cursor: pointer;
}

.main-navigation__link:hover {
  color: var(--main);
}

.submenu {
  padding-right: 20px;
  position: relative;
}

.submenu:after {
  content: "";
  position: absolute;
  width: 6px;
  height: 10px;
  right: 0;
  top: 50%;
  transform: translateY(-50%) rotate(90deg);
  mask-image: url("/assets/img/icons/stack.svg#menu-right");
  background-color: var(--black);
}

.submenu__wrapper {
  position: absolute;
  top: 99%;
  left: -24px;
  padding: 20px 24px;
  min-width: 703px;
  height: 300px;
  display: flex;
  background-color: var(--white);
  border: 1px solid #a71d1d33;
  border-radius: 24px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
  z-index: 4;
  overflow: hidden;
}

.submenu.opened + .submenu__wrapper{
  opacity: 1;
  visibility: visible;
}

.submenu.opened.submenu:after {
  right: 2px;
  top: 30%;
  transform: rotate(270deg);
  transition: transform 0.7s;
  opacity: 0.6;
}

.submenu__list {
  padding-right: 24px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 48%;
  border-right: 1px solid var(--border);
  overflow: auto;
}

.submenu__link {
  position: relative;
  display: inline-block;
  padding-right: 15px;
  width: 100%;
  cursor: pointer;
}

.submenu__link::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url("/assets/img/icons/stack.svg#menu-right");
}

.submenu__link:hover,
.submenu-services__link:hover {
  color: var(--main);
}

.submenu-services {
  position: absolute;
  left: 50%;
  top: 20px;
  padding-left: 14px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 50%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  border-bottom-right-radius: 24px;
  overflow-y: auto;
}

.opened {
  opacity: 1;
  visibility: visible;
}

.header__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  gap: 16px 0;
  align-items: center;
  flex-grow: 1;
}

.header__phone {
  margin-right: 16px;
  font-weight: 700;
  color: var(--main);
  line-height: 100%;
  display: inline-block;
  border-bottom: 1px solid var(--main);
  white-space: nowrap;
}

.header__phone:hover {
  color: var(--hover);
}

.header__icon {
  fill: var(--main);
}

.header__buttons {
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 16px;
  max-width: 380px;
}

.header__button {
  width: 222px;
}

.header__mobile {
  display: none;
}

.socials {
  &__list {
    display: flex;
    gap: 16px;
    transition: opacity 0.7s;
  }

  &__link:hover {
    opacity: 0.7;
    transition: opacity 0.7s;
  }
}

.form__wrapper {
  position: absolute;
  top: 64px;
  padding: 18px 20px;
  width: 100%;
  border-radius: 12px;
  background-color: var(--white);
  box-shadow: 0px 8px 16px 0px #4c0c0c0d;
  z-index: 3;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;

  & .form__result {
    margin-top: 0;
    margin-bottom: 16px;
  }
}

.form__wrapper.active {
  opacity: 1;
  visibility: visible;
}

@media (max-width: 1110px) {
  .logo {
    width: auto;
  }
  
  .header .logo__text {
    display: none;
  }
}

@media (max-width: 1023px) {
  .header {
    position: relative;
  }

  .header__desktop {
    display: none;
  }

  .header__mobile {
    position: fixed;
    top: 0;
    display: flex;
    flex-direction: column;
    width: 100%;
    max-height: 100%;
    overflow-y: auto;
    background-color: var(--white);
    z-index: 5;
  }

  .header__wrapper {
    padding: 12px 0;
    display: flex;
    width: 100%;
  }

  .logo {
    margin-right: auto;
    display: flex;
    align-items: center;
    gap: 11px;
    width: 151px;
    height: 50px;
  }

  .logo__img {
    width: 50px;
    height: 50px;
  }

  .header .logo__text {
    display: flex;
    font-size: 14px;
    line-height: 100%;
  }

  .logo__text span {
    font-weight: 400;
    font-size: 12px;
    color: var(--black);
  }

  .header__mobile-icon {
    margin-right: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 48px;
    height: 48px;
    border-radius: 12px;
    background-color: var(--background-icon);
  }

  .header__mobile-icon svg {
    width: 18px;
    height: 18px;
    color: var(--main);
  }

  .header__burger {
    position: relative;
    width: 48px;
    height: 48px;
    border-radius: 12px;
    background-color: var(--background-icon);
  }

  .header__burger::before {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    left: 15px;
    background-color: var(--main);
    box-shadow: 0 -6px 0 var(--main), 0 6px 0 var(--main);
    border-radius: 10px;
  }

  .header__burger--opened {
    background-color: var(--main);
    transition: background-color 1s;
  }

  .header__burger--opened.header__burger::before {
    left: 15px;
    background-color: var(--white);
    box-shadow: none;
    transform: rotate(45deg);
  }
  
  .header__burger--opened.header__burger::after {
    content: "";
    position: absolute;
    width: 18px;
    height: 2px;
    left: 15px;
    background-color: var(--white);
    transform: rotate(-45deg);
  }

  .header__menu {
    max-height: 0;
    overflow: hidden;
    background-color: var(--background);
  }

  .header__menu.opened {
    position: relative;
    padding-top: 16px;
    max-height: 100vh;
    overflow: auto;
    transition: max-height 0.7s;
  }

  .header__menu .search__input {
    background-color: var(--white);
  }

  .header__menu .form {
    margin: 0 16px 24px 16px;
    padding: 0;
    background-color: transparent;
  }

  .main-navigation {
    margin-right: 0;
    padding: 0 16px;
    width: 100%;
  }

  .main-navigation__list {
    margin-bottom: 32px;
    flex-direction: column;
    gap: 16px;
  }

  .main-navigation__item {
    position: relative;
    padding: 0;
    width: 100%;
  }

  .main-navigation__link {
    font-weight: 500;
  }

  .submenu__wrapper {
    position: static;
    top: 0;
    left: 0;
    padding: 0;
    width: 100%;
    min-width: 100%;
    height: auto;
    max-height: 0;
    display: flex;
    background-color: transparent;
    border: none;
    border-radius: 0;
    opacity: 1;
    visibility: visible;
    overflow: hidden;
  }

  .submenu.opened + .submenu__wrapper {
    padding-top: 21px;
    max-height: 100vh;
    overflow: visible;
    transition: max-height 0.5s;
  }

  .submenu__list {
    padding-right: 0;
    flex-direction: column;
    gap: 13px;
    width: 100%;
    border-right: none;
  }

  .submenu__link::before {
    content: "";
    position: absolute;
    width: 10px;
    height: 10px;
    right: 0;
    top: 0;
    transform: translateY(-50%);
    background-image: url("/assets/img/icons/stack.svg#menu-right");
    transform: rotate(90deg);
  }

  .submenu-services {
    position: static;
    left: auto;
    top: auto;
    padding-left: 0;
    gap: 13px;
    width: 100%;
    height: auto;
    background-color: transparent;
    border-bottom-right-radius: 0;
    max-height: 0;
    overflow-x: hidden;
  }

  .submenu-services.opened {
    padding-top: 21px;
    max-height: 100vh;
    overflow: visible;
    transition: max-height 0.7s;
  }

  .submenu__link.rotate.submenu__link::before {
    top: 0;
    transform: rotate(-90deg);
    opacity: 0.6;
    transition: transform 0.5s;
  }

  .header__contacts {
    padding: 20px 16px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    border-top-left-radius: 24px;
    border-top-right-radius: 24px;
    background-color: var(--main);
    color: var(--white);
  }

  .header__contacts div {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  .header__contacts p {
    font-size: 14px;
    opacity: 0.8;
  }

  .header__contacts span {
    display: block;
    font-weight: 500;
    opacity: 1;
  }

  .header__contacts-phone a {
    margin-right: auto;
    font-weight: 700;
    border-bottom: 1px solid var(--white);
    opacity: 1;
  }

  .socials {
    &__list {
      gap: 8px;
    }
  }
}