.bag {
  margin-bottom: 99px;

  &__wrapper {
    position: relative;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__subtitle {
    margin-bottom: 40px;
    padding: 0 520px;
    text-align: center;
  }

  &__img {
    margin: 0 auto;
    width: 696px;
  }

  &__cards {
    position: absolute;
    top: 188px;
    left: 0;
    width: 100%;
    max-width: 1720px;
    display: flex;
    justify-content: space-between;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 47px;
  }
}

.bag__item {
  padding: 28px 32px;
  width: 29vw;
  max-width: 557px;
  min-height: 158px;
  border-radius: 24px;
  background-color: var(--background);

  &-wrapper {
    margin-bottom: 16px;
    display: flex;
    gap: 16px;
    align-items: center;
  }

  &-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    width: 40px;
    height: 40px;
    border-radius: 8px;
    background-color: var(--background-icon);
  }

  &-title {
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
    color: var(--text-accent);
  }
}

.sinner-page {
  .bag {
    padding: 140px 0;
    background-color: var(--background);

    & .bag__item {
      background-color: var(--white);
    }
  }
}

@media (min-width: 1513px) {
  .bag__item {
    width: 557px;
  }
}

@media (max-width: 1440px) {
  .bag__subtitle {
    padding: 0;
  }

  .bag__cards {
    margin-top: -175px;
    position: static;
    gap: 12px;
    width: 100%;
  }

  .bag__list {
    width: 48%;
    gap: 24px;
  }

  .bag__item {
    width: 100%;
    max-width: 100%;
    min-height: auto;
  }
}

@media (max-width: 768px) {
  .bag {
    position: relative;
    margin-bottom: 64px;

    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 7px;
    }

    &__img {
      margin: 0;
      width: 100%;
    }

    &__img img {
      width: 100%;
      height: auto;
    }

    &__cards {
      margin-top: -75px;
      top: 0;
      left: 0;
      width: 100%;
      flex-direction: column;
      gap: 12px;
    }

    &__list {
      gap: 12px;
      width: 100%;
    }
  }

  .bag__item {
    padding: 20px 20px 18px 20px;
    width: 100%;
    line-height: 110%;

    &-title {
      font-size: 18px;
    }

    &-decsription {
      font-size: 14px;
    }
  }

  .bag--service {
    padding: 64px 0;
  }
}