// footer

.footer {
  position: relative;
  padding: 73px 0 56px 0;

  &::before {
    content: url("/assets/img/sections/background/back-footer-2.webp");
    position: absolute;
    right: 0;
    top: 0;
  }

  &::after {
    content: url("/assets/img/sections/background/back-footer-1.webp");
    position: absolute;
    left: 0;
    bottom: -5px;
  }

  &__wrapper {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 37px;
    z-index: 2;
  }

  &__top {
    display: flex;
    align-items: center;

    & p {
      font-size: 16px;
      width: 30%;
    }
  }

  & .logo {
    margin-right: 40px;
    padding-right: 40px;
    width: 254px;
    border-right: 1px solid var(--white);
  }

  & .logo__text {
    font-size: 20px;
  }

  & .logo__text,
  & .logo__text span {
    color: var(--white);
  }

  & .form {
    padding: 0;
    width: 328px;
    margin-left: auto;
    background-color: transparent;

    & .label {
      width: 100%;
    }

    & .input {
      color: var(--white);
      background-color: #ffffff1a;
    }

    .input::placeholder {
      color: var(--white);
    }
  }

  &__middle {
    display: flex;
    justify-content: space-between;
    gap: 20px;
  }

  &__flex {
    padding: 20px 24px;
    background-color: #ffffff0d;
    backdrop-filter: blur(34px);
    width: 19%;
    height: auto;
    border-radius: 24px;

    &-title {
      margin-bottom: 20px;
    }

    &-list {
      display: flex;
      flex-direction: column;
      gap: 5px;
      font-size: 16px;
    }

    & a:hover {
      opacity: 0.8;
    }

    &-item {
      opacity: 0.8;
    }
  }

  &__phone,
  &__email,
  &__address,
  &__work {
    position: relative;
    padding-left: 33px;
  }

  &__phone::before,
  &__email::before,
  &__address::before,
  &__work::before {
    content: "";
    position: absolute;
    width: 21px;
    height: 21px;
    left: 0;
    top: 0;
    background-color: var(--white);
    mask-size: 21px 21px;
  }

  &__phone::before {
    mask-image: url("/assets/img/icons/stack.svg#phone");
    mask-size: 18px 18px;
    mask-repeat: no-repeat;
  }

  &__email::before {
    mask-image: url("/assets/img/icons/stack.svg#mail");
  }

  &__address::before {
    mask-image: url("/assets/img/icons/stack.svg#address");
  }

  &__work::before {
    mask-image: url("/assets/img/icons/stack.svg#clock");
  }

  & .socials {
    margin-top: 20px;
  }

  &__button {
    margin-top: 24px;
    width: 100%;
    min-height: 52px;
  }

  &__note {
    width: 100%;
    opacity: 0.8;
  }
}

.footer-pay {
  &__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 12px;
  }

  &__item {
    padding: 12px;
    width: 47%;
    display: flex;
    flex-direction: column;
    gap: 8px;
    align-items: center;
    font-size: 16px;
    background-color: #ffffff0d;
    border-radius: 12px;

    & span {
      margin-top: auto;
      opacity: 0.8;
    }
  }
}

.footer__bottom {
  display: flex;
  font-size: 16px;
  
  &-rating {
    padding-right: 20px;
    padding-bottom: 19px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 19%;
    border-right: 1px solid #ffffff77;

    & .rating span::before {
      width: 21px;
      height: 20px;
      background-size: 21px 20px;
    }

    & p {
      font-size: 18px;
    }

    .rating span.active::before {
      background-image: url("./../img/icons/star-yellow.svg");
    }

    .rating span.active-half::before {
      background-image: url("./../img/icons/star-yellow-half.svg");
    }
  }

  &-copy {
    padding: 0 20px 19px 20px;
    width: 41%;
    border-right: 1px solid #ffffff77;
    opacity: 0.8;

    & p {
      margin-bottom: 7px;
    }
  }

  &-dop {
    padding: 0 20px 19px 20px;
    width: 38%;
    opacity: 0.8;
  }
}

@media (max-width: 1355px) {
  .footer__middle {
    justify-content: flex-start;
    flex-wrap: wrap;
  }

  .footer__flex {
    width: 32%;
  }

  .footer__flex.footer-pay {
    width: 66%;
  }
}

@media (max-width: 1032px) {
  .footer__flex {
    width: 31%;
  }

  .footer__flex.footer-pay {
    width: 64%;
  }
}

@media (max-width: 768px) {
  .footer {
    padding: 26px 0 20px 0;

    &::before,
    &::after {
    display: none;
  }

    &__top {
      flex-direction: column;

      & p {
        margin-bottom: 24px;
        font-size: 14px;
        width: 100%;
      }
    }

    & .logo {
      margin-right: 0;
      margin-bottom: 12px;
      padding-right: 0;
      padding-bottom: 12px;
      width: 100%;
      border-right: none;
      border-bottom: 1px solid #ffffff4d;
    }

    & .logo__text {
      margin: 0;
      display: flex;
      font-size: 14px;
    }

    & .form {
      width: 100%;
      margin-left: 0;
    }

    &__middle {
      flex-direction: column;
      gap: 12px;
    }

    &__flex {
      padding: 16px 20px;
      width: 100%;
      height: auto;
      border-radius: 24px;

      &-title {
        margin-bottom: 12px;
      }

      &-list {
        font-size: 14px;
      }
    }

        &__email::before,
    &__address::before,
    &__work::before {
      width: 18px;
      height: 18px;
      mask-size: 18px 18px;
    }

    &__phone::before {
      top: 2px;
      left: 2px;
      width: 16px;
      height: 16px;
      mask-size: 16px 16px;
    }

    & .socials {
      display: none;
    }

    &__button {
      margin-top: 20px;
    }
  }

  .footer__flex.footer-pay {
    width: 100%;
  }

  .footer-pay {
    &__list {
      gap: 8px;
    }

    &__item {
      font-size: 14px;
    }
  }

  .footer__bottom {
    flex-direction: column;
    font-size: 14px;
    
    &-rating {
      padding-right: 0;
      padding-bottom: 12px;
      width: 100%;
      border-right: none;

      & p {
        font-size: 16px;
      }
    }

    &-copy {
      padding: 24px 0 0 0;
      width: 100%;
      border-right: none;
      border-top: 1px solid #ffffff77;

      & p {
        margin-bottom: 7px;
      }
    }

    &-dop {
      display: none;
      padding: 24px 0 0 0;
      width: 100%;
    }
  }
}