// simptoms

.simptoms {
  position: relative;
  padding: 140px 0 78px 0;
  background-color: var(--background);

  &__title {
    position: relative;
    text-align: left;
    z-index: 2;
  }

  &__subtitle {
    position: relative;
    margin-top: auto;
    top: -62px;
    width: 40%;
    font-size: 24px;
    font-weight: 500;
    color: var(--text-accent);
    z-index: 2;
    order: 2;
  }

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 12px;
  }

  &__flex {
    padding-right: 300px;
    display: flex;
    flex-direction: column;
    width: 50%;
  }

  &__img {
    position: absolute;
    right: 48%;
    bottom: 0;
    z-index: 2;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
    width: 49%;
  }
}

.simptom {
  position: relative;
  width: 48%;
  padding: 72px 24px 26px 24px;
  font-size: 20px;
  font-weight: 600;
  background-color: var(--white);
  border-radius: 24px;

  &::before {
    content: "";
    position: absolute;
    width: 30px;
    height: 30px;
    left: 32px;
    top: 24px;
    background-image: url("/assets/img/icons/stack.svg#warning");
  }
}


@media (max-width: 1340px) {
  .simptoms__list {
    width: 45%;
  }

  .simptoms__item {
    width: 47%;
  }
}

@media (max-width: 1023px) {
  .simptoms {
    padding: 64px 0;

    &__title {
      text-align: center;
    }

    &__subtitle {
      top: 0;
      width: 100%;
      font-size: 18px;
      font-weight: 400;
      text-align: center;
    }

    &__wrapper {
      flex-direction: column;
      gap: 20px;
    }

    &__flex {
      position: relative;
      padding: 0;
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &__img {
      display: none;
    }

    &__list {
      width: 100%;
    }
  }

  .simptom {
    width: 100%;
    padding: 56px 20px 18px 20px;
    font-size: 18px;
    border-radius: 16px;

    &::before {
      width: 24px;
      height: 24px;
      left: 20px;
      top: 20px;
      background-size: 24px 24px;
    }
  }
}
