 .scroll-top {
  width: 50px;
  height: 50px;
  position: fixed;
  left: 4.3vw;
  bottom: 100px;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px solid #bf1f1f42;
  background-color: var(--white);
  box-shadow: 0 0 5px #22000231;
  opacity: 0;
  transform: rotate(-90deg);
  transition: opacity 0.7s;
  cursor: pointer;
 }

 @media (max-width: 768px) {
  .scroll-top {
    bottom: 50px;
  }
 }

 // popups

 .popup {
  height: 100vh;
  left: 50%;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translate3d(-50%, -50%, 0);
  transform: translate3d(-50%, -50%, 0);
  width: 100%;
  z-index: 1000;
  -webkit-transition: opacity 0.4s ease;
  transition: opacity 0.4s ease;
  overflow-y: auto;
  background: #2527347a;
  backdrop-filter: blur(14px);
 }

 .popup__body {
  width: 100%;
  min-height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 0 12px;
 }

 .popup.open {
  opacity: 1;
  pointer-events: all;
 }

 .popup__content {
  width: 100%;
  max-width: 917px;
  max-height: 578px;
  position: relative;
  overflow: hidden;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 52px;
  padding: 32px 52px 32px 32px;
  background-color: var(--white);
  border-radius: 24px;
  overflow: visible;
 }

 .popup__content>* {
  position: relative;
  z-index: 2;
 }

 .popup__img {
  width: 40%;
  background: var(--main) url("/assets/img/sections/background/back-popup.webp") top right no-repeat;
  border-radius: 24px;

  & picture {
    position: relative;
    top: -18px;
    margin-bottom: 18px;
  }
 }

 .popup .form {
  padding: 0;
 }

 .popup__form {
  display: flex;
  flex-direction: column;
  width: 60%;
  height: auto;
 }

 .popup__subtitle {
  margin-top: auto;
  margin-bottom: 20px;
  width: 100%;

  & a {
    font-weight: 600;
    color: var(--text-accent);

    &:hover {
     color: var(--main);
    }
  }
 }

 .popup__subtitle--small {
  margin-top: 0;
  font-size: 14px;
  line-height: 110%;
  color: #22000266;
  text-align: center;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
 }

 .popup__subtitle--small::before,
 .popup__subtitle--small::after {
  content: '';
  flex: 1;
  height: 1px;
  background-color: #22000226;
 }

 .popup--search .form {
  position: relative;
  margin-bottom: 30px;
  width: 100%;
  min-height: auto;
 }

 .popup--search .popup__title {
  padding-left: 0;
  margin-bottom: 0;
  text-align: left;
 }

 .search__input {
  width: 100%;
  color: var(--black);
  padding-right: 46px;
 }

 .popup--search .form__label {
  position: relative;
  display: block;
  width: 100%;
 }

 .popup--search {
  & .popup__content {
    padding: 40px 60px;
    flex-direction: column;
    gap: 0;
  }

  & .popup__title {
    margin-bottom: 32px;
    text-align: center;
  }

  .form__result {
    margin-top: 24px;
    margin-bottom: 16px;
    color: #22000299;
  }
 }

 .form__list {
  padding-top: 16px;
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 100%;
  max-height: 322px;
  border-top: 1px solid var(--border);
  overflow-y: auto;
 }

 .popup__close {
  cursor: pointer;
  position: absolute;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  top: -40px;
  right: -40px;
  z-index: 1000;
  width: 40px;
  height: 40px;
  background-color: #ffffff1a;
  border-radius: 50%;

  svg {
    transition: transform 0.7s;
  }

  &:hover {
    svg {
     transform: rotate(360deg);
    }
  }
 }

 .button__ok {
  margin: 0 auto;
  margin-top: 20px;
  width: 137px;
 }

 .popup-responce .popup__content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 38px;
  height: 578px;
  font-size: 24px;
  line-height: 110%;
  font-weight: 500;
  color: var(--text-accent);
 }

 @media (max-width: 768px) {
  .popup__content {
    padding: 20px;
    max-width: auto;
    max-height: auto;
  }

  .popup__img {
    display: none;
  }

  .popup__form {
    width: 100%;
  }

  .popup__title {
    margin-bottom: 24px;
  }

  .popup__subtitle {
    margin-bottom: 16px;

    & a {
     display: block;
    }
  }

  .popup__close {
    top: -56px;
    right: 0;
  }

  .close-popup {
    width: 24px;
    height: 24px;
    top: 10px;
    right: 10px;
  }

  .popup--search {
    & .popup__content {
     padding: 24px 20px;
    }

    & .popup__title {
     margin-bottom: 20px;
    }
  }

  .form__list {
    max-height: 276px;
  }

  .form__result {
    margin-top: 20px;
  }
 }

 .cookie-block {
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: var(--black);
  color: var(--white);
  padding: 10px 14px;
  font-size: 14px;
  z-index: 10151;
 }

 .cookie-block button {
  border: 0;
  outline: 0;
  padding: 16px;
  margin: 0 8px;
  min-width: 394px;
  height: 52px;
  border-radius: 5px;
  background-color: var(--main);
  border-radius: 12px;
  cursor: pointer;
 }

 .cookie-block.accepted {
  bottom: -999px;
  opacity: .2;
 }

 @media(max-width: 768px) {
  .cookie-block {
    flex-direction: column;
    gap: 10px;
    align-items: start;
    font-size: 12px;
  }
 }

 .widget {
  position: fixed;
  right: 0;
  bottom: 139px;
  z-index: 5;

  &__wrapper {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 0;
    overflow: hidden;
    transition: max-width 0.7s ease;

    &.active {
     max-width: 254px;
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 8px;
    padding: 8px;
    background-color: var(--black);
    border-top-left-radius: 16px;
    border-bottom-left-radius: 16px;
  }

  &__item {
    padding: 20px;
    border-radius: 8px;
    color: var(--white);
    background-color: #ffffff0d;
    transition: background-color 0.7s;
    white-space: nowrap;

    &:hover {
     background-color: #ffffff1a;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__icon {
    width: 24px;
    height: 24px;
    color: var(--white);
  }

  &__open {
    position: relative;
    z-index: 2;
    padding: 20px;
    width: 64px;
    height: 64px;
    background-color: var(--black);
    border-top-left-radius: 16px;
    border-bottom-left-radius: 16px;
    transition: all 0.3s;

    svg {
     transition: opacity 0.2s;
    }

    &:hover {
     opacity: 0;

     svg {
      opacity: 0;
     }

     &+.widget__wrapper {
      max-width: 270px;
     }
    }

    &.active {
     opacity: 0;
    }
  }

  &:hover {
    .widget__open {
     opacity: 0;

     svg {
      opacity: 0;
     }
    }

    .widget__wrapper {
     max-width: 270px;
    }
  }
 }

 @media (max-width: 768px) {
  .cookie-block {
    text-align: center;
  }

  .cookie-block button {
    min-width: 100%;
  }

  .widget {
    bottom: 43px;

    &__wrapper.active {
     max-width: 213px;
    }

    &__item {
     padding: 12px 10px 12px 12px;
    }

    &__link {
     display: flex;
     gap: 16px;
    }

    &__icon {
     width: 24px;
     height: 24px;
     color: var(--white);
    }
  }
 }

 // Калькулятор

 .popup-calc {
  & .popup__content {
    min-height: 578px;
    flex-direction: column;
  }

  &__wrapper {
    display: flex;
    gap: 27px;
  }

  &__slides {
    padding: 8px;
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    &:has(.popup-calc__slide--form) {
     padding: 0;
    }
  }

  .popup-calc__slide--form {
    & picture {
     margin-bottom: -18px;
    }

    & .popup__title {
     margin-top: 16px;
     margin-bottom: auto;
    }

    & .popup__subtitle {
     margin-top: 0;
     margin-bottom: 20px;
    }

    & .popup__price {
     margin-bottom: 20px;
     font-size: 32px;
     color: var(--text-accent);
    }
  }

  &__slide {
    min-width: 100%;
  }

  &__title {
    margin-bottom: 32px;
  }

  &__label {
    padding: 24px 24px 20px 24px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-weight: 600;
    border-radius: 16px;
    color: var(--text-accent);
    background-color: var(--background);
    position: relative;
    overflow: hidden;
    transition: color 0.7s, background-color 0.7s;
    cursor: pointer;

    &::before {
     content: '';
     position: absolute;
     top: 0;
     right: -20%;
     width: 80%;
     height: 100%;
     background-image: url("/assets/img/sections/background/back-radio.png");
     background-size: contain;
     background-repeat: no-repeat;
     background-position: right top;
     opacity: 0;
     transform: translateX(20px);
     transition:
      opacity 0.7s ease,
      transform 0.7s ease;
     z-index: 1;
     pointer-events: none;
    }

    &:hover {
     color: var(--white);
     background-color: #bc1d1dc9;
     transition: color 0.7s, background-color 0.7s;

     & .popup-calc__number {
      color: var(--main);
      background-color: var(--white);
     }

     & .popup-calc__icon {
      color: var(--white);

      & circle {
        fill: var(--white);
        opacity: 0.3;
      }
     }
    }

    &:has(.popup-calc__radio:checked) {
     color: var(--white);
     background-color: var(--main);
     transition: color 0.7s, background-color 0.7s;

     &::before {
      opacity: 1;
      transform: translateX(0);
     }

     & .popup-calc__number {
      color: var(--main);
      background-color: var(--white);
     }

     & .popup-calc__icon {
      color: var(--white);

      & circle {
        fill: currentColor;

        & path {
         fill: currentColor;
        }
      }
     }
    }
  }

  &__label>* {
    position: relative;
    z-index: 2;
  }

  &__radio {
    position: absolute;
    opacity: 0;
    width: 0;
    height: 0;

    &:checked+.popup-calc__number {
     color: var(--main);
     background-color: var(--white);
    }
  }

  &__form {
    display: flex;
    flex-wrap: wrap;
    gap: 13px;

    &--service {
     & .popup-calc__label {
      width: calc(33% - 10px);
      min-height: 137px;
     }
    }

    &--location {
     & .popup-calc__label {
      width: calc(50% - 13px);
      min-height: 218px;
     }
    }

    &--urgency {
     & .popup-calc__label {
      width: calc(33% - 10px);
      min-height: 185px;
     }
    }

    &--condition {
     & .popup-calc__label {
      width: calc(50% - 13px);
      min-height: 154px;
     }
    }

    &--dop {
     & .popup-calc__label {
      width: calc(33% - 10px);
      min-height: 154px;

      &.popup-calc__label:hover {
        color: var(--text-accent);
        background-color: var(--background);
      }
     }
    }
  }

  &__number {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 23px;
    height: 23px;
    font-size: 14px;
    line-height: 110%;
    border-radius: 50%;
    color: var(--white);
    background-color: var(--main-dark);
  }

  &__icon {
    color: var(--main);
  }

  &__pagination {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    border-radius: 16px;
    color: var(--text-accent);
    background-color: var(--background);
  }

  &__button {
    width: calc(50% - 16px);
  }

  &__bottom {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    gap: 16px;
  }
 }

