// narcologist

.narcologist {
  &__title {
    padding: 0 20%;
    margin-bottom: 23px;
  }

  &__subtitle {
    padding: 0 28%;
    margin-bottom: 40px;
    text-align: center;
  }

  &__list {
    display: flex;
    justify-content: space-between;
  }
}

.narcologist__item {
  padding: 34px 28px 28px 28px;
  width: 32%;
  display: flex;
  flex-direction: column;
  background-color: var(--background);
  border-radius: 24px;

  &-icon {
    margin-bottom: 29px;
    width: 48px;
    height: 48px;
  }

  &-title {
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
    color: var(--text-accent);
  }
}

@media (max-width: 1023px) {
  .narcologist {

    &__title {
      margin-bottom: 16px;
      padding: 0;
    }

    &__subtitle {
      padding: 0;
      margin-bottom: 32px;
    }

    &__list {
      flex-direction: column;
      gap: 12px;
    }
  }

  .narcologist__item {
    padding: 20px;
    width: 100%;

    &-icon {
      margin-bottom: 20px;
      width: 32px;
      height: 32px;
    }

    &-title {
      margin-bottom: 11px;
      font-size: 18px;
      line-height: 110%;
    }
  }
}