// sitemap

.sitemap {
  margin-bottom: 140px;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  &__item {
    padding: 40px 32px;
    width: calc(25% - 24px);
    border-radius: 24px;
    background-color: var(--background);

    &-title {
      margin-bottom: 32px;
      font-size: 24px;
      line-height: 110%;
      font-weight: 500;
      color: var(--text-accent);
    }

    & a:hover {
      color: var(--hover);
    }

    &-button {
      margin-top: 16px;
      font-weight: 700;
      color: var(--main);
      transition: color 0.7s;

      &.hidden {
        display: none;
      }

      &:hover {
        color: var(--black);
        transition: color 0.7s;
      }
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 16px;
    overflow: hidden;
    opacity: 0.6;
    transition: max-height 0.7s;
  }
}

@media (max-width: 1440px) {
  .sitemap__item {
    width: calc(50% - 24px);
  }
}

@media (max-width: 768px) {
  .sitemap {
    margin-bottom: 64px;

    &__wrapper {
      flex-direction: column;
      gap: 12px;
    }

    &__item {
      padding: 20px;
      width: 100%;
      border-radius: 16px;

      &-title {
        margin-bottom: 20px;
        font-size: 18px;
      }

      &-button {
        margin-top: 12px;
      }
    }

    &__list {
      gap: 8px;
    }
  }
}