// tariffs

.tariffs {
  &__title {
    margin-bottom: 20px;
  }

  &__subtitle {
    margin-bottom: 40px;
    padding: 0 25%;
    text-align: center;
    color: #b42c2c;
  }

  &__slider {
    margin-bottom: 40px;
    overflow: hidden;
  }
}

.tariff {
  padding: 12px 12px 40px 12px;
  display: flex;
  flex-direction: column;
  gap: 37px;
  height: auto;
  min-height: 673px;
  background-color: var(--background);
  border-radius: 24px;

  &__wrapper {
    padding: 24px 28px;
    border-radius: 16px;
    background-color: var(--background-icon);
    color: var(--text-accent);
  }

  &__name {
    margin-bottom: 16px;
    font-weight: 600;
    font-size: 32px;
    line-height: 110%;
  }

  &__list {
    margin: 0 28px;
  }

  &__button {
    margin: auto 28px 0 28px;
    padding: 4px;
    display: flex;
    justify-content: space-between;
    background-color: var(--main);
    border-radius: 12px;

    & .button {
      min-height: 52px;
    }
  }

  &__price {
    padding: 16px;
    width: 229px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    text-align: center;
    color: var(--text-accent);
    background-color: var(--white);
    border-radius: 8px;
  }

  &__bage {
    position: relative;
    padding: 6px 16px 6px 47px;
    height: 35px;
    border-radius: 40px;
    color: var(--text-accent);
    background-color: var(--background);

    &::before {
      content: "";
      position: absolute;
      width: 23px;
      height: 23px;
      left: 16px;
      top: 6px;
      background-image: url("/assets/img/icons/stack.svg#star");
      opacity: 0.6;
    }
  }
}

.tariff--accent {
  background-color: var(--main);
  color: var(--white);
  background-image: url("/assets/img/sections/background/back-tariff.webp");
  background-position: left bottom;
  background-repeat: no-repeat;

  & .tariff__wrapper {
    color: var(--white);
    background-color: #ffffff1a;

    & div {
      display: flex;
      justify-content: space-between;
    }
  }

  & .list li::before {
    background-color: var(--white);
  }

  & .tariff__button {
    background-color: var(--white);
  }

  & .button {
    color: var(--main);
    background-color: var(--white);
  }

  & .tariff__price {
    color: var(--white);
    background-color: var(--main);
  }
}

.pay {
  display: flex;
  gap: 24px;

  & div {
    border-radius: 24px;
    background-color: var(--background);
  }

  &__flex {
    padding: 18px 48px 22px 48px;
    display: flex;
    align-items: center;
    gap: 50px;
    flex-grow: 1;

    & p {
      font-size: 20px;
      font-weight: 600;
    }
  }

  &__list {
    padding-left: 50px;
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    border-left: 1px solid var(--border);
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 14px;
  }

  &__icon{
    width: 37px;
    height: 37px;
    color: #bf1f1f;
  }

  &__note {
    padding: 33px;
    width: 26%;
    font-size: 16px;
  }
}

@media (max-width: 1354px) {
  .tariff {
    padding: 6px 6px 20px 6px;
    gap: 20px;
    border-radius: 16px;

    &__wrapper {
      padding: 12px 14px;
      border-radius: 11px;
    }

    &__name {
      margin-bottom: 10px;
      font-size: 20px;
    }

    &__list {
      margin: 0 14px;
    }

    &__button {
      margin: auto 14px 0 14px;
      flex-direction: column;
    }

    &__price {
      padding: 12px;
      width: 100%;
    }

    &__bage {
      padding: 4px 14px;
      height: 26px;

      &::before {
        display: none;
      }
    }
  }

  .tariff--accent {
    background-image: none;

    & .tariff__button {
      background-color: var(--background);
    }

    & .button {
      background-color: var(--background);
    }
  }

  .pay {
    flex-direction: column;
    gap: 16px;
  }

  .pay__icon {
    width: 24px;
    height: 24px;
  }

  .pay__note {
    padding: 20px;
    width: 100%;
  }
}

@media (max-width: 1023px) {
  .tariffs {
    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 20px;
      padding: 0;
    }

    &__slider {
      margin-bottom: 32px;
    }
  }
  
  .tariff {
    min-height: auto;
  }

  .pay {

    & div {
      border-radius: 16px;
    }

    &__flex {
      padding: 16px 20px;
      flex-direction: column;
      align-items: flex-start;
      gap: 16px;

      & p {
        font-size: 18px;
      }
    }

    &__list {
      padding-left: 0;
      display: flex;
      flex-wrap: wrap;
      gap: 12px;
      width: 100%;
      border-left: none;
    }

    &__item {
      padding: 12px;
      gap: 8px;
      width: 47%;
      border-radius: 12px;
      background-color: var(--white);
    }

    &__note {
      padding: 16px 20px;
    }
  }
}