/* intro */

.intro {
  margin-top: -17px;
  padding-top: 17px;
  border-radius: 40px;
  overflow: hidden;
}

.intro__wrapper {
  padding-left: 80px;
  justify-content: space-between;
  display: grid;
  grid-template-columns: 648px 52%;
  min-height: 731px;
  border-radius: 40px;
  color: var(--main);
  background-color: #a71d1d1a;
}

.intro__text {
  position: relative;
  padding: 75px 0 74px 0;
  display: flex;
  flex-direction: column;
  z-index: 3;
}

.intro__title {
  margin-bottom: 32px;
}

.intro__subtitle {
  margin-bottom: 40px;
}

.intro__list {
  margin-bottom: 25px;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.intro__list li {
  position: relative;
  padding-left: 21px;
}

.intro__list li::before {
  content: url("/assets/img/icons/stack.svg#intro-list");
  position: absolute;
  left: 0;
  top: 0;
  width: 9px;
  height: 18px;
  z-index: -1;
}

.intro__button {
  margin-top: auto;
  margin-right: auto;
  padding: 4px;
  display: flex;
  justify-content: space-between;
  background-color: var(--main);
  border-radius: 12px;
}

.intro__price {
  padding: 16px 34px 13px 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 163px;
  font-weight: 600;
  color: var(--main);
  background-color: var(--background);
  border-radius: 8px;
}

.intro__background {
  position: relative;
  background-color: var(--main-dark);
  border-radius: 40px;
  height: auto;
  // min-height: 682px;
}

.intro__background::before {
  position: absolute;
  top: 0;
  right: 0;
  width: 928px;
  height: 732px;
  content: "";
  background-image: url("/assets/img/sections/background/back-intro.webp");
  background-repeat: no-repeat;
  border-radius: 40px;
}

.intro__picture {
  position: absolute;
  bottom: 0;
  right: 9%;
  max-height: 779px;
  z-index: 2;
}

.intro__note {
  position: absolute;
  top: 227px;
  left: -182px;
  padding: 8px 28px 8px 8px;
  width: 285px;
  background-color: var(--white);
  border-radius: 60px;
  outline: 10px solid #ffffff33;
  box-shadow: 20px 10px 20px 0px #a71d1d1a;
  z-index: 3;
}

.intro__note--right {
  top: 102px;
  left: auto;
  right: 80px;
}

.intro__note-icon {
  margin-right: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 77px;
  height: 77px;
  float: left;
  border-radius: 50%;
  background-color: var(--background-icon);
}

.intro__note-text {
  display: flex;
  flex-direction: column;
  gap: 4px;
  font-size: 16px;
  color: var(--main);
}

.intro__note-text span {
  font-size: 18px;
  font-weight: 600;
  color: var(--text);
}

.inner-page {
  & .intro__text {
    padding-top: 0;
  }
}

/* breadcrumbs */
.breadcrumbs {
  margin: 40px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  position: relative;
  width: 100%;
  color: var(--text-accent);
}

.breadcrumbs li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  gap: 8px;
  font-size: 16px;
}

.breadcrumbs li a {
  opacity: 0.6;
}

.breadcrumbs li a:hover {
  opacity: 0.9;
}

.breadcrumbs li::after {
  content: "/";
  display: block;
  font-size: inherit;
}

.breadcrumbs li:last-of-type::after {
  display: none;
}

.intro--small {
  margin: 0 auto;
  padding: 0;
  max-width: 1880px;

  .intro__wrapper {
    height: auto;
    min-height: 216px;
    grid-template-columns: 43% 821px;
    justify-content: space-between;
  }

  .intro__background {
    min-height: 216px;
  }

  .intro__text {
    padding: 0;
  }

  .title {
    font-size: 48px;
  }
}

.intro--about .intro__wrapper,
.intro--doctors .intro__wrapper {
  height: auto;
  min-height: 520px;
  grid-template-columns: 43% 821px;
  justify-content: space-between;
}

.intro--about .intro__picture {
  bottom: 55px;
  right: 12%;
}

.intro--doctors .intro__picture {
  bottom: 0;
  right: 16%;
}

.intro--about .intro__subtitle p:not(:last-child),
.intro--doctors .intro__subtitle p:not(:last-child) {
  margin-bottom: 16px;
}

@media (max-width: 1880px) {
  .intro__wrapper {
    padding-left: 50px;
    gap: 24px;
    grid-template-columns: 45% 50%;
  }

  .intro__picture {
    right: 0;
  }

  .intro--about .intro__picture,
  .intro--doctors .intro__picture {
    right: 0;
  }

  .intro__note {
    top: 37px;
    left: -180px;
  }

  .intro__note--right {
    top: auto;
    bottom: 68px;
    left: auto;
    right: 50px;
  }
}

@media (max-width: 1550px) {
  .intro__wrapper {
    padding-left: 16px;
  }

  .intro__picture {
    right: -15%;
  }

  .intro--about .intro__img {
    width: 75%;
    height: auto;
  }

  .intro__note {
    display: none;
  }
}

@media (max-width: 1300px) AND (min-width: 1024px) {
  .intro__picture {
    right: -30%;
  }

  .intro__list {
    padding-right: 150px;
  }

  .intro .intro__title {
    font-size: 45px;
  }

  .intro--about .intro__picture {
    right: 0;
  }

  .intro--doctors .intro__picture {
    right: 10%;
  }
}

@media (max-width: 1023px) {
  .intro {
    margin-top: 0;
    padding-top: 0;
    border-radius: 24px;
  }

  .intro__wrapper {
    padding: 0;
    display: flex;
    flex-direction: column;
    height: auto;
    border-radius: 24px;
  }

  .intro__text {
    padding: 23px 16px 27px 16px;
    width: 100%;
  }

  .intro__title {
    margin-bottom: 16px;
  }

  .intro__subtitle {
    margin-bottom: 24px;
  }

  .intro__list {
    gap: 13px;
  }

  .intro__list li {
    padding-left: 18px;
  }

  .intro__button {
    margin-top: 32px;
    width: 100%;
    height: 52px;
  }

  .intro__button .button {
    min-height: 44px;
  }

  .intro__price {
    padding: 12px 16px 11px 15px;
    min-width: 116px;
  }

  .intro__background {
    width: 100%;
    min-height: auto;
    height: 276px;
    border-radius: 24px;
  }

  .intro__picture {
    position: relative;
    top: -34px;
    left: 80px;
  }

  .intro__img {
    height: 310px;
    width: auto;
  }

  .intro__background::before {
    top: -60px;
    bottom: auto;
    right: -126px;
    background-size: 443px 350px;
    transform: rotate(90deg);
  }

  .intro__note {
    display: none;
  }

  .intro--about,
  .intro--doctors {
    .intro__text {
      margin-bottom: 55px;
    }

    .intro__background {
      min-height: 152px;
      height: 152px;
    }

    & .intro__picture {
      position: relative;
      margin: 0 auto;
      bottom: 70px;
      top: auto;
      left: auto;
      right: auto;
      width: 288px;
      height: auto;
    }

    & .intro__img {
      width: 100%;
      height: auto;
    }
  }

  .intro--doctors .intro__picture {
    width: 129%;
    max-width: 450px;
    left: -15%;
    bottom: 58%;
  }

  .intro--doctors .intro__text {
    margin-bottom: 35px;
  }

  .intro--small {
    .intro__wrapper {
      padding: 0 16px;
      height: auto;
      min-height: 125px;
      display: block;
    }

    .intro__background {
      display: none;
    }

    .title {
      font-size: 32px;
      word-wrap: break-word;
    }
  }

  /* breadcrumbs */
  .breadcrumbs {
    margin: 20px 0;
    gap: 0 8px;
  }

  .breadcrumbs li {
    font-size: 14px;
  }
}

@media (max-width: 768px) {
  .intro__picture {
    left: -40px;
    right: 35px;
  }
}