// prices-page

.prices-page {
  margin: 20px 0 140px 0;

  & .prices {

    &__wrapper {
      margin: 0 auto;
      padding: 80px;
      display: flex;
      flex-direction: column;
      gap: 12px;
      max-width: 1880px;
      border-radius: 24px;
      background-color: var(--background);
    }

    &__category {
      padding: 20px 40px;
      border-radius: 24px;
      background-color: var(--white);
      transition: 0.5s;
    }

    &__title {
      margin-bottom: 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      font-size: 24px;
      line-height: 110%;
      font-weight: 500;

      & span {
        margin-left: auto;
        margin-right: 24px;
        font-size: 18px;
        opacity: 0.6;
      }
    }

    &__open {
      position: relative;
      width: 70px;
      height: 70px;
      border-radius: 12px;
      background-color: var(--background);

      &::before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 16px;
        height: 2px;
        background-color: var(--main);
        opacity: 1;
        transition: opacity 0.7s;
      }

      &::after {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 2px;
        height: 16px;
        background-color: var(--main);
      }
    }

    &__content {
      margin-bottom: -80px;
      padding: 32px 0 22px 0;
    }

    &__list {
      width: 100%;
      border-collapse: collapse;

      &-cost {
        width: 26%;
      }

      &-button {
        width: 10%;
        font-weight: 800;
        color: var(--main);
      }

      & th {
        padding: 20px 32px;
        font-size: 16px;
        font-weight: 400;
        color: #2200029c;
        text-align: left;
      }

      & td {
        padding: 24px 32px;
        text-align: left;
      }

      & thead {
        background-color: var(--background);
      }

      & thead tr th:first-child {
        border-top-left-radius: 12px;
        border-bottom-left-radius: 12px;
      }

      & thead tr th:last-child {
        border-top-right-radius: 12px;
        border-bottom-right-radius: 12px;
      }

      & tr:nth-child(even) {
        background-color: var(--background);

        td:first-child {
          border-top-left-radius: 12px;
          border-bottom-left-radius: 12px;
        }

        td:last-child {
          border-top-right-radius: 12px;
          border-bottom-right-radius: 12px;
        }
      }
    }

    &__category[open] {
      padding-bottom: 100px;

      & .prices__open::after {
        opacity: 0;
        transition: opacity 0.5s;
      }

      & .prices__title {
        padding-bottom: 20px;
        border-bottom: 1px solid var(--border);
      }
    }
  }
}

@media (max-width: 1023px) {
  .prices-page {
    .tab {
      width: 49%;
    }
  }
}

@media (max-width: 768px) {

  .prices-page {
    margin-bottom: 64px;

    & .prices {
      &__wrapper {
        padding: 16px;
      }

      &__category {
        padding: 16px 20px;
        border-radius: 16px;
      }

      &__title {
        position: relative;
        margin-bottom: 0;
        flex-direction: column;
        align-items: flex-start;
        gap: 12px 0;
        font-size: 18px;

        & p {
          padding-right: 52px;
        }

        & span {
          margin-left: 0;
          margin-right: 0;
          font-size: 16px;
          width: 100%;
          order: 2;
        }
      }

      &__open {
        position: absolute;
        top: 0;
        right: 0;
        width: 32px;
        height: 32px;
        border-radius: 8px;

        &::before {
          width: 12px;
        }

        &::after {
          height: 12px;
        }
      }

      &__content {
        margin-bottom: -80px;
        padding: 16px 0 0 0;
      }

      &__list {
        width: 100%;
        border-collapse: collapse;

        &-name {
          width: 100%;
        }

        &-cost {
          width: 49%;
        }

        &-button {
          width: 49%;
          font-weight: 700;
        }

        & td {
          padding: 0;
        }

        & thead {
          display: none;
        }

        & tr {
          padding: 12px 16px;
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          gap: 12px 0;
          border-radius: 12px;
        }

        & tr:nth-child(even) {
          background-color: transparent;
        }

        & tr:nth-child(odd) {
          background-color: var(--background);
        }
      }

      &__category[open] {
        padding-bottom: 100px;

        & .prices__open::after {
          opacity: 0;
          transition: opacity 0.5s;
        }

        & .prices__title {
          padding-bottom: 16px;
        }
      }
    }
  }

  .prices-page .prices__list td.prices__list-button {
    text-align: right;
  }
}