.banner-consultation {
  position: relative;
  margin-bottom: 140px;
  padding: 62px 0 62px 0;

  &::before {
    content: url("/assets/img/sections/background/back-services-2.webp");
    position: absolute;
    right: 0;
    top: -72%;
  }

  &::after {
    content: url("/assets/img/sections/background/back-services-1.webp");
    position: absolute;
    left: -10%;
    top: -27%;
  }
    
  &__wrapper {
    position: relative;
    display: flex;
    justify-content: space-between;
    gap: 24px;
    z-index: 2;
  }

  &__text {
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 45%;
  }

  &__link {position: relative;
    margin-top: auto;
    margin-right: auto;
    padding-left: 68px;
  }

  &__link::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    left: 32px;
    top: 21px;
    mask-image: url("/assets/img/icons/stack.svg#phone");
    background-color: var(--main);
  }

  &__form {
    width: 557px;
  }
}

@media (max-width: 1640px) {
  .banner-consultation::after {
    display: none;
  }
}

@media (max-width: 768px) {
  .banner-consultation {
    position: relative;
    margin-bottom: 64px;
    padding: 64px 0;

    &::before {
      content: url("/assets/img/sections/background/back-mob.webp");
      right: 0;
      top: 0;
      width: 100%;
    }

    &::after {
      display: block;
      content: url("/assets/img/sections/background/back-mob.webp");
      left: 0;
      bottom: 0;
      width: 100%;
      transform: rotate(180deg);
    }
      
    &__wrapper {
      flex-direction: column;
      gap: 40px;
    }

    &__text {
      padding: 0;
      gap: 16px;
      width: 100%;
      text-align: center;
    }

    &__link {
      margin-top: 20px;
      margin-right: 0;
      padding-left: 53px;
    }

    &__link::before {
      content: "";
      position: absolute;
      width: 18px;
      height: 18px;
      left: 51px;
      top: 18px;
      background-size: 18px 18px;
    }

    &__form {
      width: 100%;
    }
  }
}