// promos

.promos {
  padding: 140px 0 140px 0;
  background-color: var(--background);

  &__title {
    margin-bottom: 40px;
  }

  &__slider {
    overflow: hidden;
  }

  &__link {
    margin-top: 32px;
  }
}

.inner-page .promos.section-offset {
  margin: 0;
}

.promo {
  position: relative;
  padding: 28px 44px 32px 32px;
  height: auto;
  min-height: 391px;
  display: flex;
  flex-direction: column;
  border-radius: 24px;
  background: var(--white) url("/assets/img/sections/background/back-promo.webp") top right no-repeat;

  &__wrapper {
    padding-right: 78px;
  }

  &__sale {
    position: absolute;
    right: 37px;
    top: 39px;
    color: var(--white);
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
  }

  &__date {
    margin-bottom: 20px;
    font-size: 16px;
    line-height: 130%;
    opacity: 0.6;
  }

  &__name {
    margin-bottom: 12px;
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
  }

  &__description {
    opacity: 0.8;
  }

  &__price {
    margin-top: auto;
    font-size: 32px;
    line-height: 100%;
  }

  &__price--old {
    position: relative;
    margin-left: 16px;
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
    color: #22000266;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      left: 0;
      right: 0;
      top: 50%;
      background-color: #ba1a1a;
    }
  }

  &__button {
    margin-top: 19px;
  }
}

@media (max-width: 768px) {
  .promos {
    padding: 64px 0;

    &__title {
      margin-bottom: 20px;
    }

    &__slider {
      position: relative;
    }
  }

  .promo {
    padding: 16px 20px 20px 20px;
    min-height: 320px;
    background-size: 75px 165px;
    border-radius: 16px;

    &__wrapper {
      padding-right: 0;
    }

    &__sale {
      top: 29px;
      right: 22px;
      font-size: 16px;
    }

    &__date {
      margin-bottom: 16px;
      font-size: 14px;
    }

    &__name {
      margin-bottom: 8px;
      padding-right: 45px;
      font-size: 18px;
    }

    &__price {
      font-size: 20px;
      line-height: 110%;
    }

    &__price--old {
      font-size: 18px;
    }
  }
}

// promos-page

.promos-page {
  margin: 60px 0 140px 0;

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  .promo {
    width: calc(33% - 14px);
    background-color: var(--background);
  }
}

@media (max-width: 1440px) {
  .promos-page .promo {
    width: 48%;
  }
}

@media (max-width: 768px) {
  .promos-page {
    margin: 40px 0 64px 0;

    &__list {
      flex-direction: column;
      gap: 12px;
    }

    .promo {
      width: 100%;
    }
  }
}