.reviews {
  position: relative;
  padding: 120px 0;

  &::before {
    content: url("/assets/img/sections/background/back-reviews-2.webp");
    position: absolute;
    right: 0;
    top: 0;
  }

  &::after {
    content: url("/assets/img/sections/background/back-reviews-1.webp");
    position: absolute;
    left: 0;
    bottom: -5px;
  }

  &__wrapper {
    position: relative;
    z-index: 2;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__title.title--secondary {
    text-align: center;
  }

  &__subtitle {
    margin-bottom: 40px;
    text-align: center;
  }

  &__slider {
    position: relative;
    height: 100%;
    overflow: hidden;
  }

  &__bottom {
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
  }

  &__link {
    margin: 0;
  }
}

.review {
  position: relative;
  padding: 32px 32px 24px 32px;
  display: flex;
  flex-direction: column;
  height: auto;
  min-height: 314px;
  color: var(--black);
  background-color: var(--white);
  border-radius: 24px;

  &__text {
    max-height: 117px;
    overflow: hidden;
    transition: max-height 0.4s ease-out;
  }

  &__button {
    margin-top: 12px;
    margin-bottom: 12px;
    font-weight: 700;
    color: var(--main);
    text-align: left;
    text-decoration: underline;

    &:hover {
      opacity: 0.8;
    }
  }

  &__button.hidden {
    display: none;
  }

  &__bottom {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
  }

  &__date {
    opacity: 0.4;
  }

  &__name {
    font-weight: 600;
    font-size: 20px;
  }
}

.rating {
  margin-bottom: 16px;
  display: flex;
  gap: 4px;
}

.rating span {
  position: relative;
  width: 24px;
  height: 24px;
}

.rating span::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  left: 0;
  top: 0;
  background-image: url("./../img/icons/star-empty.svg");
  background-size: 24px 24px;
  background-repeat: no-repeat;
  background-position: center;
}

.rating span.active::before {
  background-image: url("./../img/icons/star.svg");
}

.rating span.active-half::before {
  background-image: url("./../img/icons/star-half.svg");
}

@media (max-width: 1500px) {
  .reviews::after {
    content: "";
  }
}

@media (max-width: 1023px) {
  .reviews {
    padding: 64px 0;

    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 20px;
    }

    &::before {
      content: url("/assets/img/sections/background/back-mob.webp");
      right: 0;
      top: 0;
      width: 100%;
    }

    &::after {
      display: block;
      content: url("/assets/img/sections/background/back-mob.webp");
      left: 0;
      bottom: 0;
      width: 100%;
      transform: rotate(180deg);
    }

    &__bottom {
      margin-top: 0;
      flex-direction: column;
      gap: 32px;
    }

    & .slider-navigation {
      display: none;
    }

    & .button--more {
      margin: 0 auto;
    }
  }

  .review {
    padding: 20px;
    height: auto;
    border-radius: 16px;

    &__text {
      max-height: 189px;
    }

    &__name {
      font-size: 16px;
    }
  }

  .rating {
    margin-bottom: 16px;
  }

  .rating span {
    position: relative;
    width: 20px;
    height: 20px;
  }

  .rating span::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    left: 0;
    top: 0;
    background-size: 18px 18px;
  }
}

.reviews-page {
  margin: 60px 0 140px 0;

  &__list.active {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  & .review {
    width: calc(50% - 24px);
    background-color: var(--background);

    &--image {
      padding: 12px;

      & .review__wrapper {
        position: relative;
        width: 100%;
        height: 314px;
        border-radius: 11px;
        overflow: hidden;
      }

      & .review__img {
        width: 100%;
      }

      & .review__filter {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        border-radius: 11px;
        background-color: #22000299;
        backdrop-filter: blur(4px);
      }
    }

    &__img {
      width: 100%;
      height: auto;
      object-fit: cover;
      object-position: center top;
      transition: filter 0.7s;
    }

    & a {
      display: block;
      height: 100%;
      width: 100%;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-image: url("/assets/img/icons/stack.svg#scale");
        background-size: 50px 50px;
        background-position: center;
        background-repeat: no-repeat;
        z-index: 3;
      }

      &::after {
        content: "Посмотреть отзыв из соцсетей";
        position: absolute;
        left: 50%;
        top: 60%;
        transform: translateX(-50%);
        width: 179px;
        text-align: center;
        font-weight: 600;
        color: var(--white);
        z-index: 3;
      }
    }

    &__site {
      position: absolute;
      top: 32px;
      right: 32px;
      width: 80px;
      height: 23px;
      z-index: 2;
    }
  }
}

@media (max-width: 768px) {
  .reviews-page {
    margin: 64px 0;

    &__list.active {
      flex-direction: column;
      gap: 15px;
    }

    & .review {
      width: 100%;

      &--image {
        & .review__wrapper {
          height: 290px;
        }

        & .review__img {
          width: auto;
          height: 290px;
        }
      }

      & a {
        display: block;
        height: 100%;
        width: 100%;

        &::before {
          background-size: 40px 40px;
        }
      }

      &__site {
        top: 20px;
        right: 20px;
        width: 70px;
        height: 20px;
      }
    }
  }
}