// text-content

.text-content__wrapper {
  margin-left: auto;
  max-width: 74%;

  &>*:first-child {
    margin-top: 0;
  }

  &>*:last-child {
    margin-bottom: 0;
  }

  & ul {
    margin-bottom: 20px;
  }

  & a {
    color: var(--main);
    text-decoration: underline;
  }
}

.text-content {
  &__flex {
    display: flex;
    gap: 24px;
  }

  &__nav {
    margin-bottom: auto;
    padding: 20px 24px 9px 24px;
    width: 412px;
    border-radius: 24px;
    background-color: var(--background);

    & a {
      text-decoration: none;
      color: var(--black);
    }
  }

  &__nav-title {
    display: block;
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
    color: var(--text-accent);
  }

  & h1,
  h2,
  h3,
  h4,
  h5 {
    font-weight: 600;
    line-height: 110%;
    color: var(--text-accent);
  }

  h1 {
    margin-bottom: 24px;
    font-size: 48px;
  }

  & h2,
  h3,
  h4 {
    margin-top: 41px;
    margin-bottom: 24px;
  }

  & h2 {
    font-size: 48px;
  }

  & h3 {
    font-size: 40px;
  }

  & h4 {
    font-size: 32px;
  }

  & h5 {
    margin-top: 40px;
    margin-bottom: 24px;
    font-size: 24px;
  }

  & p {
    margin-bottom: 12px;
  }

  & ul li {
    margin-bottom: 12px;
    position: relative;
    padding-left: 22px;
  }

  & ul li::before {
    content: "";
    width: 6px;
    height: 6px;
    position: absolute;
    top: 8px;
    left: 0;
    transform: rotate(45deg);
    background-color: var(--main);
  }

  & ul ul {
    margin-top: 12px;
  }

  & ul ul li:not(:last-child) {
    margin-bottom: 8px;
  }

  & ul ul li a {
    opacity: 0.8;
  }

  & ul ul li::before {
    background-color: var(--black);
  }

  & ol {
    margin-bottom: 12px;
    list-style: decimal;
    display: flex;
    flex-direction: column;
    gap: 12px;
  }

  & ol li {
    position: relative;
    padding-left: 28px;
  }

  & ol li::marker {
    content: none;
  }

  & ol li::before {
    content: counter(list-item) ".";
    position: absolute;
    left: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 20px;
    height: 23px;
    color: var(--main);
    font-size: 18px;
    line-height: 130%;
  }

  blockquote {
    margin-top: 32px;
    margin-bottom: 32px;
    padding: 32px 40px;
    border-radius: 24px;
    background-color: var(--white);
    box-shadow: 0px 0px 24.8px 0px #ba1a1a1a inset;
  }

  table {
    margin-bottom: 24px;
    width: 100%;
    border-collapse: collapse;
    border-radius: 24px;
    position: relative;
    overflow: hidden;
    outline: 1px solid var(--border);
    outline-offset: -1px;
  }

  table th,
  table td {
    padding: 16px;
    border: 1px solid var(--border);
  }

  table th {
    background-color: var(--background-icon);
  }

  table tr:nth-child(2n) {
    background-color: var(--background);
  }

  .literature {
    margin-top: 60px;
    padding: 32px 40px 20px 40px;
    background-color: var(--background);
    border-radius: 24px;

    & h5 {
      margin: 0;
      margin-bottom: 24px;
    }
  }
}

.check {
  margin-top: 24px;
  display: flex;
  justify-content: space-between;

  & p {
    margin: 0;
  }

  &__doctor {
    padding: 20px 24px;
    width: 49%;
    display: grid;
    grid-template-columns: 120px 1fr;
    gap: 16px 20px;
    background-color: var(--background);
    border-radius: 24px;
  }

  &__top {
    grid-column: 1/-1;
    display: flex;
    justify-content: space-between;
    width: 100%;
    font-size: 14px;
    opacity: 0.8;
  }

  &__bottom {
    display: flex;
    flex-direction: column;
    grid-column: 2/3;
    width: 100%;
  }

  &__img {
    grid-column: 1/2;
    margin-right: 20px;
    width: 120px;
    height: 130px;
    border-radius: 10px;
    object-fit: cover;
  }

  & a.check__name {
    margin-bottom: 8px;
    display: block;
    font-weight: 600;
    font-size: 20px;
    color: var(--black);
    text-decoration: none;

    &:hover {
      color: var(--text-accent);
    }
  }

  &__prof,
  &__work {
    display: block;
    opacity: 0.8;
  }

  &__work {
    margin-top: auto;
  }
}

@media (max-width: 1023px) {
  .check {
    flex-direction: column;
    gap: 12px;

    &__doctor {
      position: relative;
      padding: 12px 16px 53px 16px;
      width: 100%;
      grid-template-columns: 69px 1fr;
      gap: 5px 16px;
      border-radius: 16px;
    }

    &__img {
      margin-right: 0;
      width: 69px;
      height: 81px;
    }

    &__name {
      font-size: 18px;
    }

    &__work {
      position: absolute;
      bottom: 12px;
      left: 16px;
      padding-top: 8px;
      display: flex;
      justify-content: space-between;
      width: calc(100% - 32px);
      border-top: 1px solid var(--border);
    }
  }
}

@media (max-width: 768px) {
  .text-content {
    &__flex {
      flex-direction: column;
      gap: 48px;
    }

    &__nav {
      margin-bottom: 0;
      padding: 18px 20px 9px 20px;
      width: 100%;
    }

    &__nav-title {
      margin-bottom: 16px;
      font-size: 20px;
    }

    & h2,
    h3,
    h4,
    h5 {
      margin-top: 32px;
      margin-bottom: 20px;
    }

    h1 {
      font-size: 32px;
    }

    & h2 {
      font-size: 24px;
    }

    & h3 {
      font-size: 22px;
    }

    & h4 {
      font-size: 20px;
    }

    & h5 {
      font-size: 18px;
    }

    & p {
      margin-bottom: 8px;
    }

    & ol li::before {
      font-size: 16px;
    }

    blockquote {
      margin-top: 24px;
      margin-bottom: 24px;
      padding: 20px 24px;
      border-radius: 16px;
    }

    table {
      border-radius: 12px;
    }

    .literature {
      margin-top: 40px;
      padding: 18px 20px 6px 20px;
      border-radius: 16px;

      & h5 {
        margin: 0;
        margin-bottom: 16px;
      }
    }
  }

  .text-content__wrapper {
    margin-left: 0;
    width: 100%;
    max-width: 100%;
  }
}

.text-page {
  margin: 60px 0 140px 0;

  & .text-content__wrapper {
    margin: 0;
    min-width: 100%;
  }
}

@media (max-width: 768px) {
  .text-page {
    margin: 64px 0;
  }
}