// 404

.not-found {
  margin: 0 auto;
  margin-bottom: 140px;
  width: 100%;
  max-width: 1720px;

  &__wrapper {
    display: flex;
    gap: 24px;
  }

  &__background {
    position: relative;
    width: calc(50% - 24px);
    height: 584px;
    border-radius: 24px;
    background-color: var(--main-dark);
    background-image: url("/assets/img/sections/background/back-404.webp");

    & img {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__text {
    padding: 52px 52px 60px 52px;
    display: flex;
    flex-direction: column;
    width: calc(50% - 24px);
    border-radius: 24px;
    background-color: var(--background);

    & p {
      margin-bottom: 12px;
    }
  }

  &__title {
    margin-bottom: 32px;
  }

  &__subtitle {
    margin-bottom: 40px;
    color: var(--main);
  }

  &__links {
    margin-top: auto;
    display: flex;
    gap: 24px;
  }
}

@media (max-width: 1555px) {
  .not-found__background {
    height: auto;
    min-height: 584px;
  }

  .not-found__links {
    margin-top: 32px;
    flex-direction: column;
    gap: 12px;
  }

  .not-found__link {
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
  }
}

@media (max-width: 1023px) {
  .not-found__text {
    padding: 35px 25px 35px 35px;
  }
}

@media (max-width: 768px) {
  .not-found {
    margin-bottom: 116px;

    &__background {
      display: none;
    }

    &__text {
      padding: 52px 20px 52px 20px;
      width: 100%;

      & p {
        margin-bottom: 8px;
      }
    }

    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 32px;
    }
  }
}