// gallery

.gallery {

  &__title {
    margin-bottom: 20px;
  }

  &__subtitle {
    margin-bottom: 40px;
    text-align: center;
  }

  &__slider {
    position: relative;
    margin-bottom: 32px;
    overflow: hidden;
  }

  &__slide {
    position: relative;
    height: 344px;
    // width: 24%;
    border-radius: 24px;
    overflow: hidden;

    & a {
      display: block;
      height: 100%;
      width: 100%;

      &::before {
        content: "";
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 64px;
        height: 64px;
        opacity: 0;
        background-image: url("/assets/img/icons/stack.svg#scale");
        z-index: 3;
      }

      &:hover {
        .gallery__img {
          filter: brightness(0.5);
        }

        &::before {
          opacity: 1;
        }
      }
    }
  }

  &__img {
    height: 344px;
    width: 100%;
    object-fit: cover;
    object-position: center;
    transition: all 0.3s ease;
  }
}

@media (max-width: 768px) {
  .gallery {

    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 20px;
    }

    &__slider {
      margin-bottom: 32px;
    }

    &__slide {
      height: 207px;
    }
  }
}

// gallery-page

.gallery-page {
  margin: 60px 0 140px 0;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  & .gallery__slide {
    width: calc(25% - 20px);
    height: auto;
    max-height: 352px;

    & img {
      height: 352px;
      object-fit: cover;
      object-position: center;
      overflow: hidden;
    }
  }
}

@media (max-width: 1440px) {
  .gallery-page {
    & .gallery__slide {
      width: 48%;
    }
  }
}

@media (max-width: 768px) {
  .gallery-page {
    margin: 40px 0 64px 0;

    &__wrapper {
      flex-direction: column;
    }

    .gallery__slide {
      width: 100%;

      & img {
        height: 247px;
      }
    }
  }
}