.banner-calc {
  position: relative;
  padding: 60px 0 50px 0;
  max-height: 392px;
  
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background-image: 
      url("/assets/img/sections/background/back-calc.webp");
    background-position: left top;
    background-repeat: no-repeat;
    background-size: auto 101%;
  }

  &__wrapper {
    position: relative;
    width: 39%;
    z-index: 2;
  }

  &__title {
    margin-bottom: 20px;
  }

  &__subtitle {
    margin-bottom: 48px;
    opacity: 0.8;
  }

  &__button {
    position: relative;
    padding-left: 68px;

    &::before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      left: 32px;
      top: 21px;
      mask-image: url("/assets/img/icons/stack.svg#money");
      mask-size: 20px 20px;
      background-color: var(--main);
    }
  }
}

@media (max-width: 1600px) {
  .banner-calc__wrapper {
    width: 50%;
  }
}

@media (max-width: 1260px) {
  .banner-calc__wrapper {
    width: 80%;
  }
}

@media (max-width: 768px) {
  .banner-calc {
    position: relative;
    padding: 64px 0 261px 0;
    max-height: 539px;
    
    &::before {
      background-position: right top;
      background-image: url("/assets/img/sections/background/back-calc-mob.webp");
    }

    &__wrapper {
      width: 100%;
    }

    &__title {
      margin-bottom: 16px;
    }

    &__subtitle {
      margin-bottom: 36px;
      text-align: center;
    }

    &__button {
      width: 100%;
    }
  }
}

.banner-calc__button::before {
  top: 17px;
}