// article

.article-page {
  margin-bottom: 140px;

  .text-content {
    display: flex;
    justify-content: space-between;
    gap: 60px;
  }

  .text-content__wrapper {
    margin-left: 0;
    max-width: 73%;
  }

  .text-content__nav {
    margin-bottom: 48px;
    padding: 32px 40px 20px 40px;
    width: 100%;
  }

  .text-content__flex {
    display: flex;
    flex-direction: column;
    gap: 40px;
  }

  .banner-consultation {
    margin: 80px 0;
    padding: 60px 60px 60px 80px;
  }

  .banner-consultation__form {
    width: 458px;
  }

  .banner-consultation::after {
    width: 50%;
    left: -18%;
    top: 25%;
  }

  .form__title {
    padding: 0;
  }

  .banner-consultation__text {
    width: 51%;
  }

  .text-content__flex-popular ul li {
    padding: 0;
    padding-bottom: 12px;
    border-bottom: 1px solid var(--border);
  }

  .text-content__flex-popular ul li:last-child {
    padding-bottom: 0;
    border-bottom: none;
  }

  .text-content__flex-popular ul li::before {
    display: none;
  }

  .text-content__flex-banner {
    padding: 28px 32px;
    display: flex;
    flex-direction: column;
    min-height: 290px;
    background-image: url("./../img/sections/background/back-banner-mob.webp");
    background-position: top right;

    & .text-content__flex-title {
      margin-bottom: 12px;
      color: var(--white);
      text-align: left;
      font-size: 32px;
    }
  }

  .text-content__flex-buttons {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    text-decoration: underline;

    & button {
      font-weight: 800;
      text-align: left;
    }
  }
}

.text-content__flex-form,
.text-content__flex-popular {
  padding: 28px 32px 24px 32px;
  border-radius: 24px;
  background-color: var(--background);
}

.text-content__flex-form .form {
  padding: 0;
  background-color: transparent;
}

.text-content__flex-form .input {
  background-color: var(--white);
}

.text-content__flex-title {
  margin-bottom: 24px;
  font-size: 24px;
  line-height: 110%;
  font-weight: 500;
  color: var(--main);
  text-align: center;
}

@media (max-width: 1344px) {
  .article-page .banner-consultation {
    padding: 64px 16px;
  }

  .text-content__flex-buttons {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }
}

@media (max-width: 1023px) {
  .article-page {
    margin-bottom: 64px;

    .text-content {
      display: block;
    }

    .text-content__wrapper {
      width: 100%;
      max-width: 100%;
    }

    .text-content__flex {
      display: none;
    }

    .text-content__nav {
      padding: 16px 20px 4px 20px;
    }

    .banner-consultation {
      margin: 64px 0;
    }

    .banner-consultation__form {
      width: 100%;
    }

    .banner-consultation__text {
      width: 100%;
    }

    .text-content__flex-form,
    .text-content__flex-popular {
      padding: 28px 32px 24px 32px;
      border-radius: 24px;
      background-color: var(--background);
    }

    .text-content__flex-form .form {
      padding: 0;
      background-color: transparent;
    }

    .text-content__flex-form .input {
      background-color: var(--white);
    }

    .text-content__flex-title {
      margin-bottom: 24px;
      font-size: 24px;
      line-height: 110%;
      font-weight: 500;
      color: var(--main);
      text-align: center;
    }

    .text-content__flex-popular ul li {
      padding: 0;
      padding-bottom: 12px;
      border-bottom: 1px solid var(--border);
    }

    .text-content__flex-popular ul li:last-child {
      padding-bottom: 0;
      border-bottom: none;
    }

    .text-content__flex-popular ul li::before {
      display: none;
    }

    .text-content__flex-banner {
      padding: 28px 32px;
      display: flex;
      flex-direction: column;
      min-height: 290px;

      & .text-content__flex-title {
        margin-bottom: 12px;
        color: var(--white);
        text-align: left;
        font-size: 32px;
      }
    }

    .text-content__flex-link {
      margin-top: auto;
      margin-left: auto;
      text-decoration: underline;
      background-image: url("./../img/sections/background/back-banner-mob.webp");
      background-position: top right;
    }
  }
}

// articles

.articles {
  &__list {
    margin: 60px 0 140px 0;
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }
}

.article {
  position: relative;
  padding: 24px 40px 24px 24px;
  display: flex;
  gap: 40px;
  width: 49%;
  border-radius: 24px;
  background-color: var(--background);

  &__img {
    width: 40%;
    height: 273px;
    border-radius: 8px;
    overflow: hidden;
  }

  &__img img {
    width: 100%;
    height: 273px;
    object-fit: cover;
    border-radius: 8px;
  }

  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex: 1;
  }

  &__title {
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
  }

  &__link {
    margin-top: auto;

    & span {
      position: relative;
      padding-left: 40px;
    }

    & span::before {
      content: "";
      position: absolute;
      width: 24px;
      height: 24px;
      left: 0;
      top: 0;
      background-image: url("/assets/img/icons/stack.svg#book");
    }

  }
}

@media (max-width: 1230px) {
  .article {
    padding: 20px;
    width: 48%;
    flex-direction: column;
    gap: 20px;

    &__img {
      width: 100%;
      height: 179px;
    }

    &__img img {
      height: 179px;
    }
  }
}

@media (max-width: 768px) {
  .articles {
    &__list {
      margin: 40px 0 64px 0;
      flex-direction: column;
      gap: 12px;
    }
  }

  .article {
    width: 100%;
    border-radius: 16px;

    &__wrapper {
      gap: 12px;
    }

    &__title {
      font-size: 18px;
    }

    &__link {
      margin-top: 8px;

      & span {
        padding-left: 35px;
      }

      & span::before {
        width: 19px;
        height: 19px;
        background-size: 19px 19px;
      }
    }
  }
}