// doctors

.doctors {
  padding: 140px 0;
  background-color: var(--background);

  &__title {
    margin-bottom: 40px;
  }

  &__slider {
    position: relative;
    height: 100%;
    overflow: hidden;
  }

  &__bottom {
    margin-top: 32px;
    display: flex;
    justify-content: space-between;
  }

  &__link {
    margin: 0;
  }
}

.doctor {
  position: relative;
  height: auto;
  max-height: 619px;
  display: flex;
  justify-content: center;
  text-align: center;
  border-radius: 24px;
  overflow: hidden;

  &::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    bottom: 0;
    background: linear-gradient(180deg, rgba(34, 0, 2, 0) 0%, rgba(34, 0, 2, 0.6) 100%);
    z-index: 3;
  }

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    bottom: 0;
    background-color: #22000233;
    transition: backdrop-filter 0.7s ease, background-color 0.7s ease;
    z-index: 2;
  }

  &:hover::before {
    background-color: #22000290;
    backdrop-filter: blur(24px);
    transition: backdrop-filter 0.7s, background-color 0.7s;
  }

  &__img {
    height: 619px;
    width: auto;
    object-fit: cover;
    object-position: top;
  }

  &__text {
    position: absolute;
    top: 0;
    left: 0;
    padding: 28px 32px;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    text-align: left;
    z-index: 4;
    color: var(--white);
  }

  &__name {
    margin-top: auto;
    margin-bottom: 12px;
    font-weight: 500;
    font-size: 24px;
    line-height: 110%;
  }

  &__spec {
    height: 0;
    overflow: hidden;
    transition: height 0.7s, margin-top 0.7s, padding-top 0.7s, border-top 0.7s;
  }

  &:hover .doctor__spec {
    display: block;
    margin-top: 20px;
    padding-top: 20px;
    height: auto;
    border-top: 1px solid var(--white);
    transition: height 0.7s, margin-top 0.7s, padding-top 0.7s, border-top 0.7s;
  }
}

@media (max-width: 768px) {
  .doctors {
    padding: 64px 0;

    &__title {
      margin-bottom: 20px;
    }

    &__slider {
      overflow: visible;
    }
  }

  .doctor {
    height: 427px;
    text-align: center;
    border-radius: 16px;

    &__img {
      height: auto;
      width: 100%;
      object-fit: cover;
      object-position: top;
    }

    &__text {
      padding: 16px 20px;
    }

    &:hover .doctor__spec {
      display: none;
    }
  }
}

// doctors-page

.doctors-page {
  margin: 60px 0 70px;

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  .doctor {
    width: calc(25% - 20px);
  }
}

@media (max-width: 1440px) {
  .doctors-page .doctor {
    width: calc(33% - 20px);
  }
}

@media (max-width: 1023px) {
  .doctors-page .doctor {
    width: calc(50% - 20px);
  }
}

@media (max-width: 650px) {
  .doctors-page .doctor {
    width: 100%;
  }
}

// doctor

.tabs {
  margin-bottom: 29px;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}

.tab,
.tabs__selected {
  padding: 17px 20px;
  border-radius: 10px;
  color: #220002;
  background-color: var(--background);
  transition: background-color 0.7s;
  cursor: pointer;

  &:hover {
    background-color: var(--background-icon);
  }
}

.tab--active {
  color: var(--white);
  background-color: var(--main);
  cursor: default;

  &:hover {
    background-color: var(--main);
  }
}

.tabs--mobile {
  display: none;
}

.tab-content {
  display: none;
}

.tab-content.active {
  display: flex;
}

.doctor-inner {
  &__wrapper {
    display: flex;
    flex-direction: column;
    width: 65%;
  }

  &__top {
    margin-bottom: 140px;
    display: flex;
    gap: 60px;

    &-list {
      margin-bottom: 48px;
      display: flex;
      gap: 15px;

      & li {
        padding: 20px 24px;
        display: flex;
        flex-direction: column;
        width: 24%;
        height: auto;
        min-height: 153px;
        border-radius: 24px;
        background-color: var(--background);

        & p {
          font-size: 16px;
        }

        & span {
          margin-top: auto;
          font-weight: 600;
        }
      }
    }

    & table {
      padding: 18px 28px;
      width: 100%;
      white-space: nowrap;
      border-radius: 24px;
      text-align: left;
      border: 2px solid var(--border);

      & th {
        padding: 0 24px 12px 27px;
        border-bottom: 2px solid var(--border);
        border-right: 2px solid var(--border);

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
          border-right: none;
        }
      }

      & td {
        padding: 12px 24px 0 27px;
        color: #220002b6;
        border-right: 2px solid var(--border);

        &:first-child {
          padding-left: 0;
        }

        &:last-child {
          padding-right: 0;
          border-right: none;
        }
      }
    }
  }

  &__img {
    width: 45%;
    height: auto;
    max-height: 720px;
    object-fit: cover;
    object-position: center;
    border-radius: 24px;
    overflow: hidden;
  }

  &__img img {
    height: auto;
    width: 100%;
    object-fit: cover;
    object-position: center;
    border-radius: 24px;
  }

  &__name {
    margin-bottom: 32px;
  }

  &__description {
    margin-bottom: 40px;
  }

  &__title {
    margin-bottom: 24px;
    font-size: 24px;
    line-height: 110%;
    font-weight: 500;
    color: var(--text-accent);
  }

  &__links {
    margin-top: auto;

    &-list {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      gap: 16px;
    }
  }

  &__mobile {
    display: none;
  }

  &__table {
    margin-bottom: 24px;
    overflow: auto;
  }

  &__links-item {
    padding: 8px 8px 8px 38px;
    display: flex;
    gap: 31px;
    align-items: center;
    border-radius: 16px;
    background-color: var(--background);
  }

  &__links-rating {
    display: flex;
    gap: 6px;
    align-items: center;
  }

  &__tabs {
    margin-bottom: 140px;
  }

  &__list {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  &__item {
    padding: 8px 8px 24px 8px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    width: calc(25% - 24px);
    height: auto;
    min-height: 268px;
    border-radius: 24px;
    background-color: var(--background);

    &-top {
      position: relative;
      padding: 22px 24px;
      border-radius: 16px;
      font-weight: 600;
      background-color: var(--white);

      &::before {
        content: "";
        position: absolute;
        width: 50px;
        height: 50px;
        right: 8px;
        top: 8px;
        border-radius: 8px;
        background-color: var(--background);
      }

      &::after {
        content: "";
        position: absolute;
        width: 50px;
        height: 50px;
        right: 8px;
        top: 8px;
        background-image: url("/assets/img/icons/stack.svg#edu");
        background-position: center;
        background-size: 24px 24px;
        background-repeat: no-repeat;
      }
    }

    &-bottom {
      margin-top: auto;
      padding: 0 26px;
    }
  }

  &__slider {
    position: relative;
    padding-bottom: 50px;
    height: 100%;
    overflow: hidden;

    & .document {
      height: 531px;
      overflow: hidden;
    }
  }

  &__navigation {
    margin-top: 32px;
    justify-content: center;
  }

  &+.reviews.block-accent {
    margin-bottom: 140px;
  }
}

@media (max-width: 1023px) {
  .doctor-inner {
    &__top {
      gap: 30px;
    }

    &__top-list {
      flex-wrap: wrap;
    }

    &__top-list li {
      width: calc(50% - 15px);
    }

    &__item {
      width: calc(33% - 16px);
    }
  }
}

@media (max-width: 768px) {
  .doctor-inner {
    &__wrapper {
      display: flex;
      flex-direction: column;
      width: 100%;
    }

    &__top {
      margin-bottom: 64px;
      flex-direction: column;
      gap: 32px;

      &-list {
        margin-bottom: 32px;
        flex-direction: column;
        gap: 16px;

        & li {
          padding: 16px 20px;
          gap: 12px;
          width: 100%;
          min-height: auto;
          border-radius: 16px;

          & p {
            font-size: 14px;
          }

          & span {
            margin-top: 0;
          }
        }
      }

      & table {
        padding: 24px 16px;
        border-radius: 16px;
        display: flex;
        overflow: hidden;

        & thead {
          flex-grow: 1;
        }

        & tr {
          display: flex;
          flex-direction: column;
        }

        & th {
          padding: 12px 9px 12px 0;
          font-weight: 400;
          border-bottom: 2px solid var(--border);
          border-right: 2px solid var(--border);

          &:first-child {
            padding-top: 0;
          }

          &:last-child {
            padding-bottom: 0;
            border-bottom: none;
            border-right: 2px solid var(--border);
          }
        }

        & td {
          padding: 12px 19px 12px 16px;
          border-bottom: 2px solid var(--border);
          border-right: none;

          &:first-child {
            padding-top: 0;
            padding-left: 16px;
          }

          &:last-child {
            padding-bottom: 0;
            border-bottom: none;
          }
        }
      }
    }

    &__img {
      width: 100%;
      height: auto;
      max-height: auto;
      border-radius: 16px;
    }

    &__img img {
      border-radius: 16px;
    }

    &__name {
      margin-bottom: 16px;
    }

    &__description {
      margin-bottom: 24px;
    }

    &__title {
      margin-bottom: 20px;
      font-size: 18px;
    }

    &__links {
      margin-top: 20px;

      &-list {
        flex-direction: column;
        gap: 8px;
      }
    }

    &__links-item {
      padding: 8px 8px 8px 38px;
      display: flex;
      gap: 31px;
      align-items: center;
    }

    &__links-rating {
      margin: 0 auto;
    }

    &__tabs {
      margin-bottom: 64px;
    }

    &__list {
      flex-direction: column;
      gap: 12px;
    }

    &__mobile {
      display: block;
      margin-bottom: 48px;

      & .button {
        width: 100%;
      }
    }

    &__links-list li .doctor-inner__button {
      display: none;
    }

    &__item {
      padding: 8px 8px 16px 8px;
      display: flex;
      flex-direction: column;
      width: 100%;
      height: auto;
      min-height: auto;
      border-radius: 16px;

      &-top {
        position: relative;
        padding: 20px;
        border-radius: 10px;

        &::before {
          width: 45px;
          height: 45px;
          border-radius: 4px;
        }

        &::after {
          content: "";
          position: absolute;
          width: 45px;
          height: 45px;
          background-size: 20px 20px;
        }
      }

      &-bottom {
        margin-top: 20px;
        padding: 0 12px;
      }
    }

    &__slider {
      padding-bottom: 0;

      & .document {
        height: 387px;
      }
    }

    &__navigation {
      margin-top: 32px;
      justify-content: center;
    }

    &+.reviews.block-accent {
      margin-bottom: 140px;
    }
  }

  .tabs {
    display: none;
  }

  .tabs--mobile {
    display: block;
    position: relative;
    margin-bottom: 24px;
  }

  .tabs__selected::after {
    content: "";
    position: absolute;
    top: 17px;
    right: 16px;
    width: 6px;
    height: 12px;
    mask-image: url("/assets/img/icons/stack.svg#menu-right");
    background-color: var(--black);
    transform: rotate(90deg);
    transition: transform 0.7s;
  }

  .tabs--mobile.opened .tabs__selected::after {
    transform: rotate(-90deg);
    transition: transform 0.7s;
  }

  .tabs__dropdown {
    position: absolute;
    top: calc(100% + 12px);
    left: 0;
    right: 0;
    padding: 16px 20px;
    gap: 12px;
    background: var(--white);
    border-radius: 16px;
    border: 2px solid var(--background);
    box-shadow: 8px 9px 9px 0px #c4c4c4bd;
    z-index: 3;
    display: none;

    & .tab {
      padding: 0;
      background-color: transparent;
    }

    & .tab--active {
      color: var(--black);
    }
  }

  .tabs--mobile.opened .tabs__dropdown {
    display: flex;
    flex-direction: column;
  }

  .tab,
  .tabs__selected {
    width: 100%;
    text-align: left;

    &:hover {
      background-color: var(--background);
    }
  }

  .tab-content {
    display: none;
  }

  .tab-content.active {
    display: block;
  }
}