.documents {
  &__slider {
    position: relative;
    overflow: hidden;
  }

  &__title {
    margin-bottom: 40px;
  }
}

.document {
  position: relative;
  width: 19%;
  height: 421px;
  border: 1px solid var(--border);
  border-radius: 24px;
  overflow: hidden;

  &__img {
    width: 100%;
    height: auto;
    object-fit: contain;
    border-radius: 24px;
    transition: all 0.3s ease;
  }

  & a {
    display: block;
    display: flex;
    justify-content: center;
    height: 100%;
    width: 100%;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      width: 64px;
      height: 64px;
      opacity: 0;
      background-image: url("/assets/img/icons/stack.svg#scale");
      z-index: 3;
    }

    &:hover {
      & .document__img {
        filter: brightness(0.5);
      }

      &::before {
        opacity: 1;
      }
    }
  }

  &__name {
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 20px 24px;
    border-bottom-left-radius: 24px;
    border-top-right-radius: 24px;
    font-size: 20px;
    font-weight: 600;
    color: var(--white);
    background-color: var(--main);
  }
}

@media (min-width: 769px) {
  .documents .swiper-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
  }
}

@media (max-width: 768px) {

  .documents__title {
    margin-bottom: 20px;
  }

  .documents__slider {
    overflow: visible;
  }

  .document {
    width: 100%;
    height: 385px;
    border-radius: 16px;

    &__img {
      width: 100%;
      height: auto;
    }

    &__name {
      padding: 16px 24px;
      font-size: 16px;
      border-bottom-left-radius: 16px;
    }
  }
}

// documents-page

.documents-page {
  margin: 60px 0 140px 0;

  &__wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
  }

  .document {
    width: calc(25% - 24px);
    height: 531px;

    & img {
      // width: 100%;
      height: 531px;
      object-fit: cover;
      object-position: center;
      overflow: hidden;
    }
  }
}

@media (max-width: 1640px) {
  .documents-page .document {
    width: 32%;
  }
}

@media (max-width: 1440px) {
  .documents-page .document{
    width: 48%;
  }
}

@media (max-width: 768px) {
  .documents-page {
    margin: 40px 0 64px 0;

    &__list {
      flex-direction: column;
    }

    .document {
      width: 100%;
      height: 381px;

      & img {
        width: 100%;
        height: auto;
      }
    }
  }
}